<template>

    <v-container fluid class="white text-xs-left">

        <v-layout>

            <v-flex xs12 md8 offset-md2>
                <h2 class="mt-3"><v-icon style="font-size: 25px">phone</v-icon> Contactos</h2>
                <v-card class="elevation-0 ma-1">
                    <!--Botao voltar-->
                    <v-flex xs12 offset-xs8 offset-sm10>
                        <v-btn round="" style="" small="" @click="$router.go(-1)" class="azul-principal white--text">Voltar</v-btn>
                    </v-flex>

                    <v-flex xs12>

                        <v-btn round="" outline="" color="orange" @click="color = 'orange'" class="azul-principal">
                            Contactos que Visualizei
                        </v-btn>

                        <v-btn v-if="isUserAuth && userAuth.Explicador" round="" outline="" color="blue" @click="color = 'blue'" class="azul-principal">
                            Meu contacto visualizado
                        </v-btn>

                    </v-flex>

                    <template v-if="color === 'orange'">
                        <!--Contactos visualizados hoje-->
                        <template v-if="todayContactWhereIamRequester.length">

                            <br>
                            <h3>Contactos que visualizei hoje</h3>
                            <br>
                            <template v-for="c in todayContactWhereIamRequester">
                                <v-alert outline="" :value="true" :color="color">
                                    <img :src="getUser(c.explicadorId).photoURL" alt="" height="15px" width="15px" style="border-radius: 20px">
                                    <span>{{  getUser(c.explicadorId).name }}</span> <br>
                                    <span>Email: {{  getUser(c.explicadorId).email }}</span> <br>
                                    <span>Telefone: {{  getUser(c.explicadorId).telfone }}</span> <br>
                                    <span>Dia: {{ c.created_at.getDate() + '/' + (c.created_at.getMonth() + 1) + '/' + c.created_at.getFullYear() }}</span>
                                </v-alert>
                            </template>
                        </template>
                        <template v-if="otherDaysContactWhereIamRequester.length">
                            <br>
                            <h3>Visualizacoes anteriores</h3>
                            <br>
                            <template v-for="c in otherDaysContactWhereIamRequester">

                                <v-alert outline="" :value="true" :color="color">
                                    <img :src="getUser(c.explicadorId).photoURL" alt="" height="15px" width="15px" style="border-radius: 20px">
                                    <span>{{  getUser(c.explicadorId).name }}</span> <br>
                                    <span>Email: {{  getUser(c.explicadorId).email }}</span> <br>
                                    <span>Telefone: {{  getUser(c.explicadorId).telfone }}</span> <br>
                                    <span>Dia: {{ c.created_at.getDate() + '/' + (c.created_at.getMonth() + 1) + '/' + c.created_at.getFullYear() }}</span>
                                </v-alert>
                            </template>

                        </template>

                        <v-flex v-if="!todayContactWhereIamRequester.length && !otherDaysContactWhereIamRequester.length" style="margin: 100px 0 100px ">
                            <span class="red--text">Ainda não visualizou o contacto de nenhum explicador!</span>
                        </v-flex>

                    </template>

                    <template v-if="color === 'blue'">
                        <!--Meu Contacto visualizados hoje-->
                        <template v-if="todayContactWhereIWhereRequested.length">

                            <br>
                            <h3>Utilizadores que visualizaram o meu contacto hoje!</h3>
                            <br>
                            <template v-for="c in todayContactWhereIWhereRequested" v-if="getUser(c.userRequesterId)">
                                <v-alert outline="" :value="true" :color="color">

                                    <img :src="getUser(c.userRequesterId).photoURL" alt="" height="15px" width="15px" style="border-radius: 20px">
                                    <span>{{  getUser(c.userRequesterId).name }}</span> <br>
                                    <span>Dia: {{ c.created_at.getDate() + '/' + (c.created_at.getMonth() + 1) + '/' + c.created_at.getFullYear() }}</span>
                                    <v-flex>
                                        <v-btn disabled="" flat="">Explicando</v-btn>
                                        <v-btn disabled="" flat="">Procurou-me</v-btn>
                                        <v-btn disabled="" flat="">Nunca Ligou</v-btn>
                                    </v-flex>
                                </v-alert>
                            </template>
                        </template>
                        <template v-if="otherDaysContactWhereIWhereRequested.length">
                            <br>
                            <h3>Outros dias</h3>
                            <br>
                            <template v-for="c in otherDaysContactWhereIWhereRequested" v-if="getUser(c.userRequesterId)">

                                <v-alert outline="" :value="true" :color="color">

                                    <img :src="getUser(c.userRequesterId).photoURL" alt="" height="15px" width="15px" style="border-radius: 20px">
                                    <span>{{  getUser(c.userRequesterId).name }}</span> <br>
                                    <span>Dia: {{ c.created_at.getDate() + '/' + (c.created_at.getMonth() + 1) + '/' + c.created_at.getFullYear() }}</span>

                                    <v-flex>
                                        <v-btn disabled="" flat="">Explicando</v-btn>
                                        <v-btn disabled="" flat="">Procurou-me</v-btn>
                                        <v-btn disabled="" flat="">Nunca Ligou</v-btn>
                                    </v-flex>
                                </v-alert>
                            </template>

                        </template>
                    </template>

                </v-card>
            </v-flex>
        </v-layout>

    </v-container>

</template>

<script>
    import { eventBus } from '../../main'
    export default {
        beforeCreate() {

            this.$Progress.start()
            this.$Progress.finish()

        },
        mounted ()
        {
            this.scrollTo(0)
        },
        data() {
            return {
//                showProcess: false,
                count: 0,
                color: 'orange'
            }
        },
        computed: {
            img () {
                return this.$store.getters.getImg
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            contactRequests ()
            {
                return this.$store.getters.getContactRequests
            },
            contactWhereIamRequester ()
            {
                if (this.contactRequests.length === 0) this.getContacts()

                return this.contactRequests.filter (c => {
                    return c.userRequesterId === this.userAuth.id
                })
            },

            todayContactWhereIamRequester ()
            {
                let today = new Date ()

                return this.contactWhereIamRequester.filter (c => {

                    let d1 = new Date(c.created_at.getFullYear(), c.created_at.getMonth(), c.created_at.getDate()); //data da visualizacao
                    let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                    return d1.getTime() === d2.getTime()
                })
            },

            otherDaysContactWhereIamRequester ()
            {
                let today = new Date ()

                return this.contactWhereIamRequester.filter (c => {

                    let d1 = new Date(c.created_at.getFullYear(), c.created_at.getMonth(), c.created_at.getDate()); //data da visualizacao
                    let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                    return d1.getTime() !== d2.getTime()
                })
            },

            contactWhereIWhereRequested ()
            {
                return this.contactRequests.filter (c => {
                    return c.explicadorId === this.userAuth.id
                })
            },

            todayContactWhereIWhereRequested ()
            {
                let today = new Date ()

                return this.contactWhereIWhereRequested.filter (c => {

                    let d1 = new Date(c.created_at.getFullYear(), c.created_at.getMonth(), c.created_at.getDate()); //data da visualizacao
                    let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                    return d1.getTime() === d2.getTime()
                })
            },

            otherDaysContactWhereIWhereRequested ()
            {
                let today = new Date ()

                return this.contactWhereIWhereRequested.filter (c => {

                    let d1 = new Date(c.created_at.getFullYear(), c.created_at.getMonth(), c.created_at.getDate()); //data da visualizacao
                    let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                    return d1.getTime() !== d2.getTime()
                })
            },
            users () {
                return this.$store.getters.getUsers
            },

            fakulUsers () {
                return this.$store.getters.getFakulUsers
            },
            notifications ()
            {
                let notifications = []


                if (true) {
                    notifications.push({
                        message: 'Ainda não actualizou a sua identidade',
                        text: 'Sua identidade, se voçê é \'Estudante\', \'Explicador\' ou \'Outro\'',
                        color: 'red',
                    })
                }
                if (true) {
                    notifications.push({
                        message: 'Ainda não actualizou os seus dados pessoais',
                        text: 'Seus Dados pessoais',
                        color: 'red',
                    })
                }
                if (!this.isUserAuth) return notifications
                if (!(this.userAuth.Educations && this.userAuth.Educations.level) || true) {
                    notifications.push({
                        message: 'Ainda não actualizou os seus dados académicos',
                        text: 'Seus Dados Académicos',
                        color: 'red',
                    })
                }

                if (this.userAuth.identities && this.userAuth.identities.isExplicador || true)
                {
                    if (!(this.userAuth.Explicador && this.userAuth.Explicador.subjectDetails.length)) {
                        notifications.push({
                            message: 'Ainda não registou as disciplinas que explica',
                            text: 'As disciplinas que explicas',
                            color: 'red',
                        })
                    }

                }

                if ((this.userAuth.Explicador && this.userAuth.Explicador.subjectDetails.length))
                {
                    if (!(this.userAuth.Explicador && this.userAuth.Explicador.availability)) {
                        notifications.push({
                            message: 'Ainda não actualizou a sua disponibilidade',
                            text: 'A sua disponibilidade',
                            color: 'red',
                        })
                    }

                }

                if (!this.userAuth.Locations) {
                    notifications.push({
                        message: 'Ainda não actualizou a sua localização',
                        text: 'A sua localização',
                        color: 'red',
                    })
                }
                if (!this.userAuth.emailVerified) {
                    notifications.push({
                        message: 'Ainda não confirmou o seu email',
                        text: 'O seu email precisa ser confirmado, veja a opção de confirmação e saiba mais no fim da sua página de perfil.',
                        link: '',
                        color: 'red',
                    })
                }

//                eventBus.$emit('mostrarTotalNofications', {total: notifications.length, color: 'red' }) //evento tratado no App.vue

                return notifications

            },

        },
        watch: {

            loading (value) {

//                if (!value && !this.error && this.showProcess) {
//
//                    let message = 'Parabens Nome, Voce acaba de activar a sua conta (Pro) assim para nos, voce eh um Explicador Profissional. ' +
//                        'Iremos te recomendar para as pessoas te procurarem. Se nao desejar mais ser profissional, eh  so entrar em contacto pelo: info@explicador.co.mz. Obrigado.'
//
//                    let text = 'As alteracoes para o User foram gravadas com sucesso!'
//                    let color = 'green'
//                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
//                    this.showProcess = false
//                    this.dateValid = false
//                    //Enviar email para o user.
//
//                }
            },
            notifications (value)
            {
                eventBus.$emit('mostrarTotalNofications', {total: value.length, color: 'red' }) //evento tratado no App.vue
            }
        },
        methods: {

            getContacts ()
            {
                this.$store.dispatch('getAllContactRequests')
            },

            getUser (userId)
            {
                return this.users.find(u => {
                    return u.id === userId
                })
            },

            emitirEvento (total)
            {
                eventBus.$emit('mostrarTotalNofications', {total: total, color: 'blue' })
                return ''
            },
            scrollTo (el) {

                if (isNaN(el))
                {
//                    Scroll to a certain element
                    document.querySelector(el).scrollIntoView({
                        behavior: 'smooth'
                    });
                }
                else
                {
                    window.scroll({
                        top: el,
                        left: 10,
                        behavior: 'smooth'
                    });
                }




            },
        },
    }
</script>
<style>

    .nota1 {
        font-size: 30pt;
        color: darkblue;
        border-left: solid darkblue medium;
        padding-left: 15px;
    }
    .nota2 {
        font-size: 12pt;
        font-weight: bold;
        color: grey;
        border-left: solid darkblue medium;
        padding-left: 15px;
    }
    .explicador-text-info {
        font-weight: 200;
        font-size: 14pt;
        color: red;
    }
</style>


