import { store } from "../../store/index";
export default (to, from, next) => {

    const user = store.getters.userAuth

    if (user && user.name && (user.email === 'joseeugenioseie@hotmail.com'
            || user.email === 'info@explicador.co.mz')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next ()

    } else {

        next({name: 'Login'})

    }




}


