<template>

    <v-container fluid class="white text-xs-left">

        <v-flex sm12 md8 offset-md2>
            <v-card class="elevation-0 ma-1">
                <v-layout>
                    <v-flex class="line-bottom mb-2">
                        <v-layout>
                            <!--Titulo da pagina-->
                            <v-flex xs6>
                                <span style="float: left" class="subheader">Instituicoes</span>
                            </v-flex>

                            <!--Botao voltar-->
                            <v-flex xs6>
                                <v-btn round="" style="float: right" small="" @click="$router.go(-1)" class="green white--text">Voltar</v-btn>
                            </v-flex>
                        </v-layout>
                    </v-flex>
                </v-layout>

                <!--Corpo da pagina-->
                <v-layout>
                    <v-flex xs12>

                        <institution :mainColor="mainColor"></institution>
                        <institution :mainColor="mainColor"></institution>
                        <institution :mainColor="mainColor"></institution>

                    </v-flex>

                </v-layout>
                <!--<v-card-text style="height: 100px; position: relative">-->
                    <!---->
                <!--</v-card-text>-->
            </v-card>
        </v-flex>

    </v-container>

</template>

<script>
    import { eventBus } from '../../../main'
    export default {
        beforeCreate() {

            this.$Progress.start()
            this.$Progress.finish()

        },
        mounted ()
        {
            this.scrollTo(0)
        },
        data() {
            return {
//                showProcess: false,
                count: 0,
                color: 'orange'
            }
        },
        computed: {
            img () {
                return this.$store.getters.getImg
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            users () {
                return this.$store.getters.getUsers
            },

            fakulUsers () {
                return this.$store.getters.getFakulUsers
            },
            mainColor () {
                return this.$store.getters.getMainColor('institutions')
            },


        },
        watch: {

            loading (value) {

//                if (!value && !this.error && this.showProcess) {
//
//                    let message = 'Parabens Nome, Voce acaba de activar a sua conta (Pro) assim para nos, voce eh um Explicador Profissional. ' +
//                        'Iremos te recomendar para as pessoas te procurarem. Se nao desejar mais ser profissional, eh  so entrar em contacto pelo: info@explicador.co.mz. Obrigado.'
//
//                    let text = 'As alteracoes para o User foram gravadas com sucesso!'
//                    let color = 'green'
//                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
//                    this.showProcess = false
//                    this.dateValid = false
//                    //Enviar email para o user.
//
//                }
            },
            notifications (value)
            {
                eventBus.$emit('mostrarTotalNofications', {total: value.length, color: 'red' }) //evento tratado no App.vue
            }
        },
        methods: {

            getContacts ()
            {
                this.$store.dispatch('getAllContactRequests')
            },

            getUser (userId)
            {
                return this.users.find(u => {
                    return u.id === userId
                })
            },

            emitirEvento (total)
            {
                eventBus.$emit('mostrarTotalNofications', {total: total, color: 'blue' })
                return ''
            },
            scrollTo (el) {

                if (isNaN(el))
                {
//                    Scroll to a certain element
                    document.querySelector(el).scrollIntoView({
                        behavior: 'smooth'
                    });
                }
                else
                {
                    window.scroll({
                        top: el,
                        left: 10,
                        behavior: 'smooth'
                    });
                }




            },
        },
    }
</script>
<style>

    .subheader {
        font-size: large;
    }
    .line-bottom {
        border-bottom: 2px salmon solid;
    }

</style>


