<template>

    <v-container fluid class="white text-xs-left">

        <v-flex sm12 md8 offset-md2>
            <v-card class="elevation-0 ma-1">
                <v-layout>
                    <v-flex class="line-bottom mb-2">
                        <v-layout>
                            <!--Titulo da pagina-->
                            <v-flex xs6>
                                <span style="float: left" class="subheader">Bibliotecas nacionais</span>
                            </v-flex>

                            <!--Botao voltar-->
                            <v-flex xs6>
                                <v-btn round="" style="float: right" small="" @click="$router.go(-1)" :class="mainColor" class="white--text">Voltar</v-btn>
                            </v-flex>
                        </v-layout>
                    </v-flex>
                </v-layout>

                <v-layout>
                    <v-flex xs12 class="ma-2">
                        <span class="subheader">Consulte a disponibilidade do(s) livro(s) antes de se deslocar até a biblioteca!</span>
                    </v-flex>
                </v-layout>

                <!--Corpo da pagina-->
                <v-layout>
                    <v-flex xs12>

                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>
                        <library :mainColor="'white'"></library>

                    </v-flex>

                </v-layout>

            </v-card>
        </v-flex>

    </v-container>

</template>

<script>
    import { eventBus } from '../../../main'
    export default {
        beforeCreate() {

            this.$Progress.start()
            this.$Progress.finish()

        },
        mounted ()
        {
            this.scrollTo(0)
        },
        data() {
            return {
//                showProcess: false,
                count: 0,
                color: 'orange'
            }
        },
        computed: {
            img () {
                return this.$store.getters.getImg
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            mainColor () {
                return this.$store.getters.getMainColor('library')
            },


            users () {
                return this.$store.getters.getUsers
            },

            fakulUsers () {
                return this.$store.getters.getFakulUsers
            },


        },
        watch: {

        },
        methods: {


            scrollTo (el) {

                if (isNaN(el))
                {
//                    Scroll to a certain element
                    document.querySelector(el).scrollIntoView({
                        behavior: 'smooth'
                    });
                }
                else
                {
                    window.scroll({
                        top: el,
                        left: 10,
                        behavior: 'smooth'
                    });
                }




            },
        },
    }
</script>
<style>

    .subheader {
        font-size: large;
    }
    /*@media screen and (max-width: 600px){*/
        /*.subheader {*/
            /*font-size: 11px;*/
        /*}*/
    /*}*/
    .line-bottom {
        border-bottom: 2px salmon solid;
    }
</style>


