            axios.post(
                'https://www.googleapis.com/identitytoolkit/v3/relyingparty/signupNewUser?key=AIzaSyDBfaoaVP6S4_fWwzsSzKhR3rc-fiMtY9w',
                {email: value.email, password: value.password, returnSecureToken: true} )
                .then((user) => {

                    value.uid = user.data.localId //Pegando id da response.
                    dispatch('saveOnlineUserData', value)


                })
                .catch((error) => {
                    alert('ocorreu um erro.')
                })


