<template>

    <div style="width: 100%">

        <v-flex xs12 sm12>

            <v-card>

                <!--Cabecalho-->
                <v-card-title class="orange darken-1">
                    <span class="headline black--text">{{ isMyProfile ? 'Minha carreira Profissional' : 'Historico Profissional do ' + nomeUser }}</span>

                    <v-spacer></v-spacer>
                    <div v-if="isMyProfile" class="white" style="border-radius: 100px; padding: 2px">
                        <v-progress-circular
                                :size="circularSize"
                                :width="7"
                                :rotate="360"
                                title="Percentagem de preenchimento"
                                style="float: right;cursor: pointer"
                                :value="personalInfoFillPercent"
                                :color="personalInfoFillPercent === 100 ? 'green': 'red'"
                                @click="toggle = !toggle"
                        >
                            <span v-if="personalInfoFillPercent === 100 && toggle">{{ personalInfoFillPercent }}</span>
                            <span v-else-if="personalInfoFillPercent === 100" title="Os seus dados académicos estão actualizados!" ><v-icon>done</v-icon></span>
                            <span v-else="" title="Clique em editar, para preencher os dados">{{ personalInfoFillPercent }}</span>
                        </v-progress-circular>
                    </div>
                    <explicador-edit-profissional-info-dialog
                        v-if="isMyProfile"
                        :user="user"
                        :isMobile="true"
                        ></explicador-edit-profissional-info-dialog>
                    <template v-else="">
                        <v-btn
                                fab=""
                                flat=""
                                small=""
                                outline=""
                                style="float: right;"
                        >
                            <v-icon class="white--text">more_horiz</v-icon>
                        </v-btn>
                    </template>

                </v-card-title>

                <v-list v-if="user.professionalHistory">

                    <template v-for=" period in user.professionalHistory.periods">

                        <v-subheader :class="{'green--text' : timeFeedBack(period) === 'Actividade actual' }">{{ timeFeedBack(period) }}</v-subheader>
                        <v-list-tile @click="">
                            <v-list-tile-action>
                                <v-icon>home</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>{{ period.institution }}</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>

                        <v-list-tile @click="">
                            <v-list-tile-action>
                                <v-icon>school</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>{{ period.task }}, ( {{ period.details }} )</v-list-tile-title>
                            </v-list-tile-content>
                        </v-list-tile>

                        <v-list-tile @click="">
                            <v-list-tile-action>
                                <v-icon>date_range</v-icon>
                            </v-list-tile-action>
                            <v-list-tile-content>
                                <v-list-tile-title>De: {{ getDate ( period.start_at ) }} a {{ getDate ( period.end_at ) }}</v-list-tile-title>
                            </v-list-tile-content>


                        </v-list-tile>

                        <v-flex v-if="isMyProfile" xs12 class=" ma-2 ml-4">
                            <explicador-edit-profissional-info-dialog
                                    :user="user"
                                    :editPeriod="period"
                                    :isMobile="true"
                            ></explicador-edit-profissional-info-dialog>
                            <v-btn @click="onDelete(period)" icon="" title="Deletar por completo"><v-icon>delete</v-icon></v-btn>

                        </v-flex>

                        <v-divider inset></v-divider>
                    </template>

                </v-list>

            </v-card>

        </v-flex>


    </div>

</template>

<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'name', 'circularSize'],
        data: () => ({
            hidden: false,
            nameU: '',
            toggle: true,
            showProcess: false,
        }),
        created () {
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    easing: 'easeInQuint'
                }
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },

            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },

            hasEducation ()
            {
                if (!this.isUser) return false
                return this.user.Educations && this.user.Educations.level
            },
            titleInstitution () {
                const defaultTitle = 'Universidade'

                if (!this.hasEducation) return defaultTitle

                let titulo = this.user.Educations.level === 'Primário' ? 'Escola' : defaultTitle
                titulo = this.user.Educations.level === 'Secundário' ? 'Escola' : titulo
                titulo = this.user.Educations.level === 'Técnico' ? 'Instituto' : titulo
                titulo = this.user.Educations.level === 'Outro' ? 'Instituicao' : titulo

                return titulo
            },

            titleEducationType () {
                const defaultTitle = 'Curso'

                if (!this.hasEducation) return defaultTitle

                let titulo = this.user.Educations.level === 'Primário' ? 'Classe' : defaultTitle
                titulo = this.user.Educations.level === 'Secundário' ? 'Classe' : titulo
                titulo = this.user.Educations.level === 'Técnico' ? 'Ano' : titulo
                titulo = this.user.Educations.level === 'Outro' ? 'Instituicao' : titulo

                return titulo
            },

            titleEducationStateOrDepartment () {
                const defaultTitle = 'Departamento'

                if (!this.hasEducation) return defaultTitle

                let titulo = this.user.Educations.level === 'Primário' ? 'Estado' : defaultTitle
                titulo = this.user.Educations.level === 'Secundário' ? 'Estado' : titulo
                titulo = this.user.Educations.level === 'Técnico' ? 'Estado' : titulo
                titulo = this.user.Educations.level === 'Outro' ? 'Estado' : titulo

                return titulo
            },

            userInstitution () {

                const defaultTitle = 'Sem Instituição'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userSchool : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userSchool : inst
                inst = userEd.level === 'Técnico' ? userEd.institution : inst
                inst = userEd.level === 'Superior' ? userEd.institution : inst
                inst = userEd.level === 'Outro' ? userEd.institution : inst

                return inst
            },
            userCourseOrClass () {

                const defaultTitle = 'Sem Faculdade'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.userClass : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.userClass : inst
                inst = userEd.level === 'Técnico' ? userEd.courseNivel : inst
                inst = userEd.level === 'Superior' ? userEd.courseNivel : inst
                inst = userEd.level === 'Outro' ? userEd.userSchool : inst

                return inst
            },
            userDepartmentOrState () {

                const defaultTitle = 'Sem Departamento'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.educationState : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.educationState : inst
                inst = userEd.level === 'Técnico' ? userEd.educationState : inst
                inst = userEd.level === 'Superior' ? userEd.department : inst
                inst = userEd.level === 'Outro' ? userEd.educationState : inst

                return inst

            },
            userNivel () {

                const defaultTitle = 'Sem Nivel'

                if (!this.hasEducation) return defaultTitle

                let userEd = this.user.Educations

                let inst = userEd.level === 'Primário' ? userEd.level : defaultTitle
                inst = userEd.level === 'Secundário' ? userEd.level : inst
                inst = userEd.level === 'Técnico' ? userEd.level : inst
                inst = userEd.level === 'Superior' ? userEd.level + ', ' + userEd.courseNivel : inst
                inst = userEd.level === 'Outro' ? userEd.level : inst

                return inst


            },

            userSubjects () {

                const negative = []

                if (!this.isUser) return negative

                let subjects = this.user.Educations.subjects

                return subjects


            },

            personalInfoFillPercent ()
            {
                if (!(this.isUserAuth && this.userAuth.Educations)) return 0

                let totalPercent = 0

                let nivel = this.userAuth.Educations.level
                if (nivel) {
                    totalPercent += 20
                }

                if (nivel === 'Superior')
                {
                    if (this.userAuth.Educations.institution) {
                        totalPercent += 15
                    }
                    if (this.userAuth.Educations.courseNivel) {
                        totalPercent += 15
                    }
                    if (this.userAuth.Educations.faculty) {
                        totalPercent += 15
                    }
                    if (this.userAuth.Educations.department) {
                        totalPercent += 10
                    }
                    if (this.userAuth.Educations.educationState) {
                        totalPercent += 15
                    }
                    if (this.userAuth.Educations.educationState !== 'em curso' || this.userAuth.Educations.subjects) {
                        totalPercent += 10
                    }
                }
                else if (nivel === 'Primário' || nivel === 'Secundário')
                {
                    if (this.userAuth.Educations.userSchool) {
                        totalPercent += 25
                    }
                    if (this.userAuth.Educations.userClass) {
                        totalPercent += 30
                    }
                    if (this.userAuth.Educations.educationState) {
                        totalPercent += 25
                    }
                }
                else if (nivel === 'Técnico' || nivel === 'Outro')
                {
                    if (this.userAuth.Educations.institution || this.userAuth.Educations.userSchool) {
                        totalPercent += 20
                    }
                    if (this.userAuth.Educations.userClass || this.userAuth.Educations.courseNivel) {
                        totalPercent += 20
                    }
                    if (this.userAuth.Educations.educationState) {
                        totalPercent += 20
                    }
                    if (this.userAuth.Educations.educationState !== 'em curso' || this.userAuth.Educations.subjects) {
                        totalPercent += 20
                    }
                }


                return totalPercent
            }

        },
        watch: {
            loading (value) {

                if (!value && !this.error && this.showProcess) {

                    let text = "Os dados profissionais foram removidos com sucesso.!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showProcess = false
                    this.dialog = false
                }
            },
        },
        methods: {

            getDate (date)
            {
                let d = new Date(date)
                let day = d.getDate().toString().length > 1 ? d.getDate() : '0' + d.getDate()

                let monthLess = parseInt(d.getMonth());
                monthLess ++

                let month = monthLess.toString().length > 1 ? monthLess : '0' + monthLess
                return day + '/' + month + '/' + d.getFullYear()
            },
            timeFeedBack (period)
            {

                let today = new Date()
                let endProDate = new Date(period.end_at)

                let d1 = new Date(endProDate.getFullYear(), endProDate.getMonth(), endProDate.getDate());
                let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                if (d1.getTime() > d2.getTime())
                {
                    return 'Actividade actual'
                }

                return 'No passado'

            },
            onDelete(period)
            {
                if (this.user.professionalHistory.periods.includes(period) && confirm('Deseja remover essas info. Profissionais?'))
                {
                    let periods = this.user.professionalHistory.periods
                    periods.splice( periods.indexOf(period), 1 );

                    this.user.professionalHistory.updated_at = new Date()
                    this.user.professionalHistory.periods = periods

                    this.showProcess = true
                    this.$store.dispatch('updateOnlineUserData', this.user)
                }

            },
            scrollTo (top) {

                window.scroll({
                    top: top,
                    left: 10,
                    behavior: 'smooth'
                });

                // Scroll to a certain element
//                document.querySelector(el).scrollIntoView({
//                    behavior: 'smooth'
//                });

            },

        },
    }
</script>
<style scoped>
    div.card__title {
        margin-top: 0px;
        padding: 0;
        font-size: 8pt;
    }
    .list__tile__title {
        font-size: 11px!important;
    }
    .headline {
        margin-left: 1em;
    }
    .explicador-bar-fixed {
        z-index: 0;
    }
    .explicador-edit-item {
        float: right;
        cursor: pointer
    }
    .icon {
        font-size: 18px;
    }

    .container {
        padding: 0px;
    }


</style>


