<template>
    <v-dialog v-model="dialog" width="450px">
        <v-list-tile
                title="Disciplinas que ele explica"
                slot="activator"
                @click.stop="mostrarDialog"
                >
            <v-list-tile-title>{{ btnText }} </v-list-tile-title>
        </v-list-tile>

        <v-card>
            <!--Titulo da dialog-->
            <v-card-title
               class="blue white--text py-4 title">
                Disciplinas do {{ user.name }}
                <v-spacer></v-spacer>
                <v-btn small="" title="Fechar" @click="dialog = !dialog" class="mb-0 mt-0" icon=""><v-icon>close</v-icon></v-btn>
            </v-card-title>

            <explain-subjects-details :user="user" :selectedNivel="selectedNivel"></explain-subjects-details>

            <v-divider class="mb-2"></v-divider>

            <v-card-actions class="pt-0">
                <v-btn flat color="primary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>

                <v-btn
                    flat
                    @click="dialog = !dialog"
                    class="blue--text"
                    :disabled="loading"
                    :loading="loading">
                    Fechar
                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        created () {
        },
        props: ['user', 'btnText','selectedNivel'],
        data () {
            return {
                dialog: false,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isExplicador () {
                return this.user && this.user.Explicador
            },
            hasSubjects () {

                return this.isExplicador && this.user.Explicador.explainLevels
            },
            subjects () {

                let negative = []
                if (!this.hasSubjects) return negative

                return this.user.Educations.subjects
            }
        },
        methods: {

            onDismissed () {
                this.$store.dispatch('clearError')
            },
            onHelp () {
                this.dialog = !this.dialog
                this.$router.push('/help')
            },
            mostrarDialog () {
                this.dialog = !this.dialog
            },

        },

    }
</script>


