<template>
    <v-layout>
        <v-flex xs12 class="mb-1">
            <v-card :class="mainColor" color="darken-2" class="white--text">
                <v-container fluid grid-list-lg>
                    <v-layout row>
                        <v-flex xs7>
                            <div>
                                <div class="subheader">Tecnicol Moz</div>
                                <div class="">Centro de Formacao</div>
                                <div>Das 7:30 - 20:00</div>
                                <div>Website: <a style="text-decoration: none; color: black" href="http://tecnicol.co.mz/" target="_blank">tecnicol.co.mz</a></div>
                                <div>Av. Eduardo Mondlane</div>
                            </div>
                        </v-flex>
                        <v-flex xs5>
                            <v-card-media
                                    src="https://vuetifyjs.com/static/doc-images/cards/foster.jpg"
                                    height="125px"
                                    contain
                            ></v-card-media>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
    export default {
        props: ['mainColor']
    }
</script>
<style>
    .subheader {
        color: black;
    }
</style>


