<template>
    <div class="text-xs-center">
        <v-menu offset-y xs3>

            <v-btn title="Encontre Bibliotecas, Centros e os testes passados" icon class="hidden-xs-only" slot="activator">
                <v-badge left :color="''">
                    <span slot="badge"><span class="notification-text">new</span></span>
                    <v-icon>apps</v-icon>
                </v-badge>

                <!--<v-icon>apps</v-icon>-->
            </v-btn>


            <v-card >

                <v-list class="pb-0">

                    <!--Explicadores-->
                <v-list-tile class="list-small-height" v-if="route !== 'Home'" @click="goToRoute('Home')">
                    <v-list-tile-action>
                        <v-btn icon ripple>
                            <v-icon color="lighten-1">school</v-icon>
                        </v-btn>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Explicadores</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <!--Instituicoes troca com explicadores-->
                <v-list-tile class="list-small-height" v-if="route !== 'Institutions'" @click="goToRoute('Institutions')">
                    <v-list-tile-action>
                        <v-btn icon ripple>
                            <v-icon color="lighten-1">account_balance</v-icon>
                        </v-btn>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Instituicoes</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <!--Bibliotecas-->
                <v-list-tile class="list-small-height" v-if="route !== 'Library'" @click="goToRoute('Library')">
                        <v-list-tile-action>
                            <v-btn icon ripple>
                                <v-icon color="lighten-1">library_books</v-icon>
                            </v-btn>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Bibliotecas</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <!--Testes Passados-->
                <v-list-tile class="list-small-height" title="Clique para encontrar os testes passados!">
                    <v-list-tile-action>
                        <v-btn icon ripple>
                            <v-icon color="lighten-1">edit</v-icon>
                        </v-btn>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <a style="text-decoration: none; font-size: 12px; color: #263238" href="https://fakul.co.mz" target="_blank">Testes Passados</a>
                    </v-list-tile-content>
                </v-list-tile>


            </v-list>

                <!--Colocando esse nao vai colocar a barra de scroll no dropdown-->
                <v-card-actions></v-card-actions>

            </v-card>

        </v-menu>
    </div>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        data: () => ({
            fav: true,
            menu: false,
            message: false,
            hints: true
        }),
        computed: {

            imgDefault () {
                return this.$store.getters.getImg
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            user () {
                return this.$store.getters.user
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.userAuth.id === this.user.id
            },
            userPendingForAuth () {
                return this.$store.getters.userPendingForAuth
            },
            isPendingForAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isAdminUser ()
            {
                return this.isUserAuth && (this.userAuth.email === 'joseeugenioseie@hotmail.com'
                || this.userAuth.email === 'info@explicador.co.mz')
            },
            route () {
                return this.$route.name
            },
            isMyProfilePage () {
                return this.route === 'Profile' && this.isMyProfile
            },
            availableText ()
            {

                if (this.isUserAuth && this.userAuth.Explicador && !this.userAuth.Explicador.availability) return 'Disponível'

                if (this.isUserAuth && this.userAuth.Explicador && this.userAuth.Explicador.availability && this.userAuth.Explicador.availability.available)
                {
                    return 'Actualmente Disponível!'
                }
                else
                {
                    return 'Indisponivel - Voçê está no modo anónimo'
                }
            },
            isAvailable ()
            {
                if (this.isUserAuth && this.userAuth.Explicador && !this.userAuth.Explicador.availability) return true

                return this.isUserAuth && this.userAuth.Explicador && this.userAuth.Explicador.availability && this.userAuth.Explicador.availability.available
            },
        },
        watch: {
            userAuth (value) {

                if (value === null) //Se for true, entao o user saiu da sua conta
                {
                    this.notifyUser() //Entao notificamos a ele que ja nao esta mais aut
                }
            }
        },
        methods: {
            logOutUser () {
//                if (confirm('Deseja realmente sair da sua conta?'))
                this.$store.dispatch('logOutUser')

            },
            onRecomend ()
            {
                eventBus.$emit('sendRecomendation') //Emitindo evento para chamar a dialog para o envio da recomendacao,
            },
            goToProfile ()
            {
                if (this.isUserAuth)
                this.$router.push({name: 'Profile', params: {profile: this.userAuth.username}})
            },
            goToRoute (route)
            {
                if (route === 'fakul')
                {
                    alert('Clicou-se no facul')
//                    location.href = 'fakul.co.mz'
                }
                this.$router.push({name: route})
            },
            notifyUser ()
            {
                let text = "Voce não está mais autenticado!"
                let color = 'blue'
                eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
            }
        }
    }
</script>


