import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import ExplicadorIndex from '@/components/profiles/Index'
import Login from '@/components/auth/Login2'
import Register from '@/components/auth/Register2'
import EmailVerified from '@/components/auth/EmailVerified'
import Help from '@/components/shared/Help'
import Settings from '@/components/settings/Settings'
import Administrator from '@/components/administrator/Index.vue'
import Notifications from '@/components/notifications/Index.vue'
import Contacts from '@/components/contacts/Index.vue'
//==========================================================
//====================== Apps ==============================
import Institutions from '@/components/apps/institutions/Index.vue'
import Library from '@/components/apps/libraries/Index.vue'

// Routers Guards
import HomeGuard from './guards/home-guard'
import LoginGuard from './guards/login-guard'
import RegisterGuard from './guards/register-guard'
import ProfileGuard from './guards/profile-guard'
import HelpGuard from './guards/help-guard'
import SettingsGuard from './guards/settings-guard'
import AdministratorGuard from './guards/adminstrator-guard'
import NotificationsGuard from './guards/notifications-guard'
import ContactsGuard from './guards/contacts-guard'

Vue.use(Router)

export default new Router({
  routes: [
      {
          path: '/',
          name: 'Home',
          component: Home,
          beforeEnter: HomeGuard
      },
      {
          path: '/:profile',
          name: 'Profile',
          component: ExplicadorIndex,
          beforeEnter: ProfileGuard
      },
      {
          path: '/login',
          name: 'Login',
          component: Login,
          beforeEnter: LoginGuard
      },
      {
          path: '/register',
          name: 'Register',
          component: Register,
          beforeEnter: RegisterGuard
      },
      {
          path: '/help',
          name: 'Help',
          component: Help,
          beforeEnter: HelpGuard
      },
      {
          path: '/settings',
          name: 'Settings',
          component: Settings,
          beforeEnter: SettingsGuard
      },
      {
          path: '/admin',
          name: 'Admin',
          component: Administrator,
          beforeEnter: AdministratorGuard
      },
      {
          path: '/notifications',
          name: 'Notifications',
          component: Notifications,
          beforeEnter: NotificationsGuard
      },
      {
          path: '/contacts',
          name: 'Contacts',
          component: Contacts,
          beforeEnter: ContactsGuard
      },

      //============================================================================================
      //======================================= Apps ===============================================
      {
          path: '/institutions',
          name: 'Institutions',
          component: Institutions,
          // beforeEnter: ContactsGuard
      },
      {
          path: '/library',
          name: 'Library',
          component: Library,
          // beforeEnter: ContactsGuard
      },
  ],
    // mode: 'history'
})



