<template>
    <v-flex>

        <v-card class="pb-2">

            <!--{{ user.Educations }}-->
            <!--Div que adiciona um padding-top para a imagem-->
            <div v-if="isUserProNow(user) || isProExample" style="background: #3E50B4" ><v-subheader class="white--text">Recomendado</v-subheader></div>
            <div v-else="isUserProNow(user)" style="height: 45px"></div>

            <!--Mostra o budge de Testemunho (um certinho) -->
            <v-badge
                 v-if="user.testemonials && user.testemonials.is"
                 left
                 title="Ele já explicou algumas pessoas e gostaram"
                 color="green"
                 style="cursor: pointer"
                 @click=""
                 class="badje-adjust" overlap>
                <v-icon @click="" slot="badge" dark small>done</v-icon>
            </v-badge>

            <!--Mostra a profissao do Explicador-->
            <v-badge
                 v-if="getProfissionalTasks(user).length"
                 left
                 :title="getProfissionalTasks(user)"
                 color="blue"
                 style="cursor: pointer"
                 @click=""
                 class="badje-adjust" overlap>
                <span slot="badge">{{ getProfissionalTasks(user).charAt(0) }}</span>
            </v-badge>
            <div v-else="" class="ma-2 pa-1"></div>

            <div class="div-img">
                <!--Imagem do utilizador-->
                <v-card-media
                        :src="img"
                        class="img-radius"
                        style="cursor: pointer"
                        @click="goToProfile()"
                >
                </v-card-media>
            </div>

            <img v-show="false" :src="img" id="imagem"/>

            <!--===============================================================-->
            <!--===============Resultado  de filtro no centro =================-->
            <!--===============================================================-->
            <!--Esse vai mostrar os resultadis de filtro-->
            <v-flex xs12 class="ma-2" v-if="searchedData || (filterType && filterName)">
                <small class="ml-3 mr-3">Nivel <b>{{ selectedNivel }}</b>: </small>
                <small class="ml-3"><a class="orange--text" @click="onCloseFilter()">Limpar Filtro</a></small>
                <br>
                <v-subheader v-if="filterName" class="grey black--text" style="border-radius: 5px">{{ searchedData ? searchedData + ' - ' : ''}} {{ filterName  | filterInstitution }}</v-subheader>
                <v-subheader v-else="" class="grey black--text" style="border-radius: 5px"> {{ searchedData }} </v-subheader>


                <!--Bairro do explicador-->
                <small class="ml-3 mr-3">{{ userLocation }}</small>

                <!--Btn contacto do explicador-->
                <explicador-show-contact-dialog
                    v-if="!isMyProfile"
                    style="float: right"
                    :title="'Ver o contacto do \'' + nomeUser + '\''"
                    :user="user"
                    :canPersonalize="false"></explicador-show-contact-dialog>

            </v-flex>

            <!--Div que adiciona um padding entre a imagem e nome do utilizador, mas so fica visiveis no dispositivos menores-->
            <div class="pt-exp hidden-sm-and-up"></div>

                    <!--Informacoes do utilizador-->
                    <v-card-title primary-title class="mt-4">
                        <div>
                            <span
                                :title="userCourse + ' - ' + userFaculty"
                                class=""
                                :class="hasFacultyOrClass ? 'grey--text' : 'red--text'"
                            >{{ userCourse | filterInstitution }}</span>
                            <div
                                 :title="nomeUser"
                                 class="headline"
                                 :class="nomeUser !== 'Sem Nome' ? '' : ' red--text' ">
                                {{ nomeUser | filterName }}
                            </div>
                            <span
                                    :title="userInstitution"
                                class=""
                                :class="userInstitution === 'Sem Universidade (Escola)' ? 'red--text' : 'grey--text'"
                            >{{ userInstitution | filterInstitution }}</span>
                        </div>
                    </v-card-title>

            <!--Botoes para acoes-->
            <v-card-actions>

                <!--Botao que leva ao perfil-->
                <v-btn
                    small
                    round
                    flat
                    outline
                    class="blue blue--text elevation-0 "
                    @click="goToProfile()"
                    title="Ver perfil">Perfil</v-btn>

                <v-spacer></v-spacer>

                <!--Opcao que activa disciplinas em baixo-->
                <!--<v-tooltip top>-->
                    <!--<v-btn icon slot="activator" @click.native="show = !show">-->
                        <!--<v-icon>library_books</v-icon>-->
                    <!--</v-btn>-->
                    <!--<span>Ver disciplinas</span>-->
                <!--</v-tooltip>-->

                <!--Menu que aparece num dos botoes-->
                <v-menu bottom left>
                    <v-btn icon ripple slot="activator">
                        <v-icon>more_vert</v-icon>
                    </v-btn>

                    <!--Opcoes de menu que aparecem depois de click-->
                    <v-list>
                            <v-list-tile
                                title="Ir para a página de perfil deste explicador"
                                @click="goToProfile()">
                                <v-list-tile-title>Ver perfil</v-list-tile-title>
                            </v-list-tile>

                            <!--Opcao da lista: 'Disciplinas'-->
                            <explicador-subject-dialog :user="user" btnText="Disciplinas" :selectedNivel="selectedNivel"></explicador-subject-dialog>

                            <v-list-tile
                                title="Descubra mais"
                                @click="onHelp">
                                <v-list-tile-title>Ajuda </v-list-tile-title>
                            </v-list-tile>

                    </v-list>
                </v-menu>

            </v-card-actions>

            <!--Disciplinas que ficam ocultas-->
            <v-slide-y-transition>
                <v-card-text v-show="show">

                    <v-list v-if="userSubjects.length > 0">
                        <template v-for="(s, i) in userSubjects" v-if="i < 3">
                            <v-subheader v-if="i === 0">Disciplinas</v-subheader>
                            <!--<v-divider inset="true"></v-divider>-->
                            <v-list-tile @click="">
                                <v-list-tile-content :title="s.name">
                                    <v-list-tile-title v-html="s.name"></v-list-tile-title>
                                </v-list-tile-content>
                            </v-list-tile>

                        </template>
                        <!--Opcao da lista: 'Disciplinas'-->
                        <explicador-subject-dialog
                            v-if="userSubjects.length > 3"
                            :user="user"
                            btnText="Ver Todas Disciplinas"></explicador-subject-dialog>
                    </v-list>
                    <div v-else>
                        {{ user.bio }}
                    </div>


                </v-card-text>
            </v-slide-y-transition>
        </v-card>

    </v-flex>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'isProExample', 'filterType', 'filterName', 'searchedData', 'selectedNivel'],
        data: () => ({
            show: false,
            auth_img3_100x100: 'https://user-images.githubusercontent.com/18400142/39090637-e2e747fc-45e4-11e8-8675-0a2ab6fb4013.png',
        }),
        mounted () {

        },

        computed: {
            smallImageUrl ()
            {
                return this.$store.getters.getSmallImageUrl
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.user && this.user.id === this.userAuth.id
            },
            img () {
                return this.user !== undefined && this.user.photoURLServer ? this.smallImageUrl + '' + this.user.photoURLServer :
                    this.user !== undefined ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.user.name ? this.user.name : 'Sem Nome'
            },
            hasSomeLocation () {

                return this.user.Locations

            },
            userLocation ()
            {
                const negative = ''

                return this.userBairro ? this.userBairro
                        : this.userLocality ? this.userLocality : negative
            },
            userBairro() {

                if (!this.hasSomeLocation) return false

                let userBairro = this.user.Locations.bairro

                return userBairro !== null && userBairro ? userBairro : false

            },
            userLocality() {

                if (!this.hasSomeLocation) return false

                let userL = this.user.Locations.locality

                return userL !== null && userL ? userL : false

            },
            hasFacultyOrClass ()
            {
                let ed = this.user.Educations

                return ed && (ed.faculty || ed.userClass)
            },

            userInstitution () {

                let inst = this.user.Educations
                let institution = inst && inst.level && inst.level === 'Superior' && inst.institution ? inst.institution  : 'Sem Instituição de Ensino'
                institution = inst && inst.level && inst.level === 'Primário' && inst.userSchool ? inst.userSchool  : institution
                institution = inst && inst.level && inst.level === 'Secundário' && inst.userSchool ? inst.userSchool  : institution
                institution = inst && inst.level && inst.level === 'Técnico' && inst.institution ? inst.institution  : institution
                institution = inst && inst.level && inst.level === 'Outro' && inst.institution ? inst.institution  : institution

                return institution
            },
            userFaculty () {

                let inst = this.user.Educations
                let institution = inst && inst.level && inst.level === 'Superior' && inst.faculty ? inst.faculty  : 'Sem faculdade'
                institution = inst && inst.level && inst.level === 'Primário' && inst.userClass ? inst.userClass + ' classe' : institution
                institution = inst && inst.level && inst.level === 'Secundário' && inst.userClass ? inst.userClass + ' classe'  : institution
                institution = inst && inst.level && inst.level === 'Técnico' && inst.department ? inst.department  : institution
                institution = inst && inst.level && inst.level === 'Outro' && inst.institution ? inst.institution  : institution

                return institution
            },
            userCourse () {

                let inst = this.user.Educations
                let institution = inst && inst.level && inst.level === 'Superior' && inst.course ? inst.course  : 'Curso não registado'
                institution = inst && inst.level && inst.level === 'Primário' && inst.userClass ? inst.userClass + ' classe' : institution
                institution = inst && inst.level && inst.level === 'Secundário' && inst.userClass ? inst.userClass + ' classe'  : institution
                institution = inst && inst.level && inst.level === 'Técnico' && inst.course ? inst.course  : institution
                institution = inst && inst.level && inst.level === 'Outro' && inst.institution ? inst.institution  : institution

                return institution
            },
            userSubjects () {
                return this.user.Educations && this.user.Educations.subjects ? this.user.Educations.subjects : []
            },
            textColor () {
                return this.userInstitution === 'Sem Universidade (Escola)' ? 'red--text' : 'grey--text'
            }
        },
        methods: {

            goToProfile () {
                if (this.user.username)
                {
                    this.$router.push('/' + this.user.username )
                }
                else
                {
                    alert('O perfil deste Explicador, por enquanto nao esta disponivel.')
                }
            },
            onHelp () {
                this.$store.dispatch('setShowHelpExplicador', true)
            },
            onCloseFilter () {
                // Reseta os criterios de pesquisa de dados na pagina, para o default
                // Este evento eh tratado nos metodos created () dos Componentes Home.vue e MainMenuLeft.vue
//                this.search = ''

                eventBus.$emit('resetExplicadoresFilter'); //evento tratado no App.vue e Home.vue
            },

            onSeeConfirmationDetails () {

                const title = 'Utilizador Confirmado'
                const message = this.nomeUser + ', "confirmado" o que significa? NOTA: Significa que os seus dados de contacto ' +
                    'foram confirmados, ou seja, pode contactar o ' + this.nomeUser + ' se ele estiver disponivel podera responder-lhe! ' +
                    'Aconselhamos-te a escolher primeiro os explicador que possuem os seus dados confirmados!'
                const btn1 = 'Cancelar'
                const btn2 = 'Fechar'
                const color = 'primary'

                /**
                 * Primeiro param: true: significa abrir a dialog
                 * Segundo param:  title: o titulo a mostrar
                 * Terceiro param:  msg: a mensagem a mostrar
                 */
                this.$store.dispatch('togleConfirmDialog', {width: '250px', show: true, color: color, title: title, message: message,btn1: btn1,btn2: btn2})

            },

            isUserProNow (user)
            {
                return this.$store.getters.isUserProNow(user)
            },

            getProfissionalTasks (user)
            {

                let periods = user.professionalHistory ? user.professionalHistory.periods : undefined

                if (periods) {

                    let today = new Date()
                    let recentTasks = ''
                    let previousTasks = ''

                    periods.forEach((period) => {

                        let endAtDate = new Date(period.end_at)

                        let d1 = new Date(endAtDate.getFullYear(), endAtDate.getMonth(), endAtDate.getDate());
                        let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                        if (d1.getTime() >= d2.getTime())
                        {
                            recentTasks += recentTasks.length > 0 ? ', ' : ''
                            recentTasks += period.task + ' na ' + period.institution
                        }
                        else
                        {
                            previousTasks += previousTasks.length > 0 ? ', ' : ''
                            previousTasks += period.task
                        }

                    })

                    return recentTasks.length > 0 ? recentTasks : previousTasks

                }
                return ''

            },

        }
    }
</script>
<style scoped>
    .orange--explicador {
        background: rgba(0,0,0,.3)!important;
    }
    .headline {
        font-size: 18px!important;
        font-weight: 400;
        line-height: 32px!important;
        letter-spacing: normal!important;
    }
    .img-radius, .div-img {
        display: block;
        margin-left: auto;
        margin-right: auto;
        border-radius: 100%;
        width: 90px;
        height: 90px!important;
    }
    div-img {
        border-radius: 100px;
    }

    .pt-exp {
        padding-top: 15px;
    }

    .badje-adjust {
        background: red!important;
        padding-left: 10px;
        margin: 2px 1px 1px 15px
    }

    @media screen and (max-width: 595px) {
        .img-radius {
            display: block;
            margin-left: auto;
            margin-right: auto;
            border-radius: 100%;
            width: 110px;
            height: 110px!important;
        }
    }

</style>


