<template>
    <v-layout>
        <v-flex xs12 class="mb-">
            <v-card :class="mainColor" color="darken-2" class="">
                <v-container fluid grid-list-lg>
                    <v-layout row>
                        <v-flex xs7 >
                            <div>
                                <div class="headline">Biblioteca central Brasao Mazula</div>
                                <div>Aberto das 8:00 - 22:00</div>
                            </div>
                            <a href="#" style="color: #263238">Pesquisar livros</a>
                            |
                            <a href="#" style="color: #263238">Ver a pagina da bilioteca</a>
                        </v-flex>
                        <v-flex xs5>
                            <v-card-media
                                    src="https://vuetifyjs.com/static/doc-images/cards/foster.jpg"
                                    height="50px"
                                    contain
                            ></v-card-media>
                        </v-flex>
                    </v-layout>
                </v-container>
            </v-card>
        </v-flex>
    </v-layout>
</template>
<script>
    export default {
        props: ['mainColor']
    }
</script>


