<template>

    <v-flex xs12 class="white photo-background">
        <v-container class="">
            <v-layout>
                <v-flex md12 class="text-xs-center hidden-xs-only white--text" style="">


                    <h1>
                        <img width="25" src="https://explicador.co.mz/assets/images/explicador-lapis-1-150x150.png"
                             alt="">
                        <span class="mb-2">Explicador</span>
                    </h1>
                    <h3>Os melhores explicadores estão aqui, <b class="blue--text">Registe-se</b> já e junte-se a nós!</h3>

                    <p class="ml-3">Ao criar a sua conta, voçê concorda com os termos e condições <a @click="onClick('#')">Saiba mais</a></p>
                </v-flex>
                <v-flex xs12 md6 class="">
                    <v-layout row v-if="error" :class="{ 'mb-4 mt-4' : error }">
                        <v-flex class="red">
                            <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                        </v-flex>
                    </v-layout>

                    <v-card class="pa-0 ma-0 mb-1" :class="elevation" style="border-radius: 15px">
                        <!--Titulo do formulario-->
                        <v-card-text class="text-xs-center pb-3">
                            <!--<img src="https://explicador.co.mz/assets/images/explicador-lapis-1-150x150.png"-->
                                 <!--alt="">-->
                            <h4 style="color: darkgray;border-bottom: solid #eee 1px" class="pb-3">Criação de conta</h4>
                        </v-card-text>
                        <!--Corpo-->
                        <v-card-text class="ma-0 pa-2 pr-4 pl-4">
                            <v-container class="ma-0 pa-0">
                                <form @submit.prevent="onSignup">

                                    <!--Redes sociais-->
                                    <v-layout row class="ma-0 pa-0">
                                        <v-flex xs12>
                                            <v-btn flat small="" round block  class="yellow" outline @click="googleSignIn" :disabled="loading">
                                                Google
                                            </v-btn>
                                        </v-flex>

                                        <v-flex xs12>
                                            <v-btn flat small="" round block  class="blue" outline @click="facebookSignIn" :disabled="loading">
                                                Facebook
                                            </v-btn>
                                        </v-flex>
                                    </v-layout>

                                    <div class="text-xs-center mt-1">OR</div>

                                    <v-layout row>
                                        <v-flex xs12>
                                            <v-text-field
                                                    label="Seu email"
                                                    prepend-icon="mail"
                                                    v-model="email"
                                                    type="email"
                                                    required></v-text-field>
                                        </v-flex>
                                    </v-layout>
                                    <v-layout row>
                                        <v-flex xs12>
                                            <v-text-field
                                                    name="password"
                                                    label="Password"
                                                    prepend-icon="lock"
                                                    v-model="password"
                                                    type="password"
                                                    required></v-text-field>
                                        </v-flex>
                                    </v-layout>
                                    <v-layout row>
                                        <v-flex xs12>
                                            <v-text-field
                                                    label="Confirmar Password"
                                                    prepend-icon="lock"
                                                    id="confirmPassword"
                                                    v-model="confirmPassword"
                                                    type="password"
                                                    :rules="[comparePasswords]"></v-text-field>
                                        </v-flex>
                                    </v-layout>

                                    <v-layout row class="mt-2">

                                        <v-flex xs12 sm3>
                                            <!--Botao cancelar-->
                                            <v-btn block style="height: 22px" small="" round="" flat="" outline="" to="/">
                                                Cancelar
                                            </v-btn>

                                        </v-flex>

                                        <v-flex xs12 sm3>
                                            <!--Botao Entrar-->
                                            <v-btn
                                                    flat
                                                    small=""
                                                    round=""
                                                    outline=""
                                                    block=""
                                                    class="ml-3"
                                                    style="height: 22px"
                                                    color="green"
                                                    type="submit"
                                                    :disabled="isRegisterProcessing && (loading || !password || password !== confirmPassword) || comparePasswords.length > 0" :loading="loading && isRegisterProcessing">
                                                Entrar
                                                <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                            </v-btn>

                                        </v-flex>
                                        <v-spacer></v-spacer>
                                    </v-layout>

                                </form>
                            </v-container>
                        </v-card-text>

                        <!--Opcao de login para quem ja possui uma conta-->
                        <v-card-text>
                            <small>Criando a conta, voce concorda com os <a @click="onClick('#')">termos e condições</a> para uso da plataforma. </small>

                            <!--<small style="color: darkgray"> <v-icon>help</v-icon><v-btn block="" flat="" round="" router to="/login">Clique aqui para login</v-btn></small>-->

                            <p>Já criou a sua conta? <a href="#" @click="onClick('Login')">Entrar</a></p>
                        </v-card-text>
                    </v-card>

                </v-flex>
            </v-layout>
        </v-container>

        <!--Dialog com explicacao da diferenca-->
        <template>
            <v-layout row justify-center>
                <v-dialog v-model="dialogInfo" width="600px">
                    <v-card>
                        <v-card-title>

                            <h2>Termos e Condições</h2>
                        </v-card-title>
                        <v-card-text class="">
                            <span class="">Criando a conta na <b>eXplicador</b>, você concorda com os seguintes termos e condições, que servem de contrato entre nós e você.</span>

                            <br>
                            <br>
                            <span class="headline">Termos & Condições</span>

                            <br>
                            <br>
                            Leia atentamente os termos e condições de navegação na plataforma eXplicador, definidos para um relacionamento saudável para consigo e as políticas da plataforma.

                            <br>
                            <br>
                            A <b>eXplicador</b> é uma inovação tecnológica desenvolvida para responder algumas necessidades do ensino e aprendizagem no percurso da formação académica, facilitando na procura de meios para esclarecimento de conteúdos não assimilados em sessão de aula. Oferecendo uma grande equipe de eXplicadores profissionais e vocacionados em diferentes área, níveis e disciplinas de estudo.
                            O nosso maior foco é contribuir no desenvolvimento da educação, levando tecnologias para além fronteiras na facilitação da procura de conteúdos pedagógicos, acreditando que a aplicação das TIC’s no dia-a-dia do estudante proporciona ao alcance de melhor proveito.


                            <br>
                            <br>
                            <span class="headline">Termos</span>
                            <br>
                            <br>
                            •	É de acesso grátis para todos docentes, investigadores, estudantes e o público em geral a navegação nesta plataforma;
                            <br>
                            •	A solicitação de serviços oferecidos pela eXplicador procederá de frequentes envios de e-mail de alertas e segurança, publicidades e informações de atualizações em sua conta mail;
                            <br>
                            •	A eXplicador assegura-se em competência a confidencialidade de quaisquer serviço solicitado e no contexto garantir a execução do mesmo fornecendo um código de visita ao utente e ao eXplicador (staff), a confrontar na materialização de serviço;
                            <br>
                            •	Solicitar informações ou esclarecimentos sobre quaisquer componentes do software que esteja em mal funcionamento ou considerada suspeita, irregular, ilícita ou ilegal;

                            <br>
                            <br>
                            <br>
                            <span class="headline">Condições</span>

                            <br>
                            <br>
                            •	Para a legitimidade do acesso e fornecimento a rigor dos serviços eXplicador o utente deve registrar-se na plataforma, preenchendo em campos mandatários informações precisas para a validação do seu registro;
                            <br>
                            •	Confidenciar todas políticas de navegação da plataforma a terceiros, a fim de que as informações sejam de rigor e de oferta segura;
                            <br>
                            •	A solitação de um eXplicador (staff) deve ser feita mediante a apresentação dos pré-requisitos mandatários;
                            <br>
                            •	Não fornecer ao eXplicador (staff) informações de inclusão ou participação de inquéritos, entrevistas sobre o eXplicador que solicitara, a fim de que os serviços fornecidos não sejam de constante percurso;
                            <br>
                            •	A eXplicador guarda-se não a par de algum envolvimento intimo ou relação emergente através do nosso canal a responsabilidade de resolução de litígios que possam futuramente emergir entre o solicitador(a) dos serviços e o eXplicador (staff);
                            <br>
                            •	Reportar com máxima urgência quaisquer comportamentos suspeitos do eXplicador (staff) no exerço do serviço solicitado;
                            <br>
                            •	Quaisquer informações suspeita, ilícita e ou ilegal fornecida pelo utente e ou eXplicador (staff) que a eXplicador entenda como ameaça serão resolvidos segundo as politicas estabelecias para a segurança da plataforma.
                            <br>
                            •	Assegurar pelo funcionamento integrado da plataforma e da sua segurança;
                            <br>
                            •	Concordar, cumprir e fazer cumprir com todas políticas da eXplicador, como parte inerente da livre navegação;
                            <br>
                            •	Confirmar sempre através do código de visita se o representante da eXplicador é mesmo solicitado por si.


                            <br>
                            <br>
                        </v-card-text>
                        <v-card-actions>
                            <v-spacer></v-spacer>
                            <v-btn color="green darken-1" flat="flat" @click="dialogInfo = false">Sim Concordo.</v-btn>
                        </v-card-actions>
                    </v-card>
                </v-dialog>
            </v-layout>
        </template>

    </v-flex>

</template>

<script>
    import { eventBus } from '../../main'
    export default {

        data () {
            return {
                email: '',
                password: '',
                confirmPassword: '',
                dialogInfo: false, //Controla a abertura da dialog
                recomended: false,
                isRegisterProcessing: false,
                elevation: 'elevation-3'
            }
        },
        computed: {
            comparePasswords () {
                return this.password !== this.confirmPassword
                    ? 'Senha de confirmacao nao corresponde' : ''
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            }
        },
        watch: {
            loading (value) {

                if (!value && !this.error && this.isRegisterProcessing) {

                    this.isRegisterProcessing = false
                    let text = "Enviamos um email de confirmacao para si, confirme clicado no link"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color,10000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar

                }
            }

        },
        methods: {
            onSignup () {
                this.isRegisterProcessing = true
                this.$store.dispatch('createUser', {email: this.email, password: this.password, recomended: this.recomended})
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            googleSignIn () {
                this.$store.dispatch('googleSignIn')
            },
            facebookSignIn () {
                this.$store.dispatch('facebookSignIn')
            },
            onClick (route)
            {
                if (route === '#')
                {
                    this.dialogInfo = true
                    return
                }
                this.$router.push({name: route})
            }
        },
        beforeCreate () {
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
            this.$Progress.finish()
        },
    }
</script>
<style scoped="">
    .photo-background{
        background: url(https://user-images.githubusercontent.com/18400142/41173831-98202a7e-6b58-11e8-84a8-73701e2f88c2.jpg) no-repeat;

    }
</style>


