<template>
    <v-dialog v-model="dialog" persistent max-width="500px">

        <v-btn
                small=""
                slot="activator"
                :color="isMobile ? '' : 'blue'"
                :flat="isMobile"
                :icon="!!editPeriod"
                :title="editPeriod ? 'Editar esses dados' : 'Adicionar Novo'"
                @click.stop="dialog = !dialog"
        >
            {{ editPeriod ? '' : 'Adicionar' }}
            <v-icon>{{ editPeriod ? 'edit' : 'add' }}</v-icon>
        </v-btn>

        <v-card>
            <!--Cabecalho da dialog-->
            <v-card-title class="headline">
                Seus dados Profissionais
                <v-spacer></v-spacer>
                <v-btn title="Fechar" @click="dialog = !dialog" class="mb-0 mt-0" icon=""><v-icon>close</v-icon></v-btn>
            </v-card-title>

            <!--Separador-->
            <v-divider></v-divider>

            <v-container grid-list-sm class="pa-4 mb-5">
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <v-card-text>
                    <v-container grid-list-md>
                        <v-layout wrap>
                            <v-flex xs12>

                                <v-select
                                        label="Identificacao Profissional"
                                        required
                                        autocomplete
                                        v-model="task"
                                        :items="['Monitor', 'Assistente', 'Professor', 'Investigador', 'Docente', 'Explicador']"
                                ></v-select>
                            </v-flex>

                            <v-flex xs12>
                                <v-text-field
                                        label="Em que instituicao?"
                                        v-model="institution"
                                        type="text"
                                ></v-text-field>
                            </v-flex>

                            <!--Data de inicio-->
                            <v-flex xs12>
                                <small>Selecionar a data de inicio:</small>
                                <v-dialog
                                        ref="dialog"
                                        persistent
                                        v-model="modal"
                                        lazy
                                        full-width
                                        width="290px"
                                        :return-value.sync="date"
                                >
                                    <v-text-field
                                            slot="activator"
                                            :label="start_at ? start_at : 'Clique para selecionar a data de inicio'"
                                            hint="A data de inicio deve ser hoje ou depois"
                                            v-model="date"
                                            prepend-icon="event"
                                            readonly
                                    ></v-text-field>
                                    <v-date-picker header-color="blue" v-model="date" scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn flat color="primary" @click="modal = false">Cancel</v-btn>
                                        <v-btn flat color="green" @click="saveStartDate(date)">OK</v-btn>
                                    </v-date-picker>
                                </v-dialog>
                            </v-flex>

                            <!--Data de Fim-->
                            <v-flex xs12 v-if="start_at">
                                <small>Selecione a data do Fim:</small>
                                <v-dialog
                                        ref="dialog"
                                        persistent
                                        v-model="modalEnd"
                                        lazy
                                        full-width
                                        width="290px"
                                        :return-value.sync="dateEnd"
                                        :disabled="false"
                                >
                                    <v-text-field
                                            slot="activator"
                                            :label="end_at ? end_at : 'Clique para selecionar a data de fim'"
                                            v-model="dateEnd"
                                            prepend-icon="event"
                                            readonly
                                    ></v-text-field>
                                    <v-date-picker header-color="blue" v-model="dateEnd" scrollable>
                                        <v-spacer></v-spacer>
                                        <v-btn flat color="primary" @click="modalEnd = false">Cancel</v-btn>
                                        <v-btn flat color="green" @click="saveEndDate(dateEnd)">OK</v-btn>
                                    </v-date-picker>
                                </v-dialog>
                            </v-flex>

                            <v-text-field
                                label="Outros Detalhes"
                                v-model="details"
                                type="text"
                                hint="A disciplina e outros detalhes relavantes"
                            ></v-text-field>

                        </v-layout>
                    </v-container>
                    <!--<small>Todos os campos obrigatorios</small>-->
                </v-card-text>


            </v-container>

            <v-divider></v-divider>

            <v-card-actions>
                <v-btn flat color="primary" @click="onHelp">Ajuda</v-btn>
                <v-spacer></v-spacer>
                <v-btn flat color="primary" @click="onCancel">Fechar</v-btn>
                <v-btn
                    flat
                    @click="saveProfissional(userAuth)"
                    class="green--text"
                    :disabled="loading"
                    :loading="loading"
                >
                    Salvar

                    <span slot="loader" class="custom-loader">
                        <v-icon light>cached</v-icon>
                    </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'isMobile', 'editPeriod'],
        data () {
            return {

                dialog: false,
                start_at: null,
                end_at: null, //Data em que expira a conta profissional, sera calculado em funcao dos dias passados
                institution: '',
                task: '',
                details: '',
                date: '',
                dateEnd: '',
                modalEnd: '',
                modal: false,
                showProcess: false,

                //Remover isso
                province: '',
                city: '',
                post: '',
                municipalDistrict: '',
                bairro: '',
                locality: '',
                district: '',
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },

            isFromCity () {
                return this.province === 'Maputo Cidade'
            },

            isFromMaputoProvince () {
                return this.province === 'Maputo Província'
            },
            bairroLabel () {
                let generic = 'Sua Localidade'
                if (this.province === '') return generic

                if (this.isFromCity || this.isFromMaputoProvince)
                {
                    generic = 'Seu Bairro'
                }
                return generic
            },

            provinceNames () {
                return this.$store.getters.getProvinceNames
            },

            districtNames () {
                let dists = this.$store.getters.getDistrictNames

                if (dists.length || !this.dialog)
                {
                    return dists
                }
                this.$store.dispatch('getAllDistricts')
                return this.$store.getters.getDistrictNames.sort()

            },

            bairroMCNames () {
                return this.$store.getters.getMCBairrosNames
            },

            bairroMPNames () {
                return this.$store.getters.getMPBairrosNames
            },

            municipalDistrictNames () {
                let distMun = this.$store.getters.getMunicipalDistrictNames

                if (distMun.length || !this.dialog)
                {
                    return this.$store.getters.getMunicipalDistrictNames
                }

                this.$store.dispatch('getAllMunicipalDistricts')
                return this.$store.getters.getMunicipalDistrictNames
            },

            hasSomeLocation () {

                if ( !this.isUser ) return false

                return this.user.Locations

            },

        },
        watch: {

            loading (value) {

                if (!value && !this.error && this.showProcess) {

                    let text = "Os seus dados Profissionais foram gravados com sucesso!"
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showProcess = false
                    this.dialog = false
                }
            },
            dialog (value)
            {
                if (value && this.editPeriod) {

                    this.inicializeUserInfo()
                }
                else //Entao a dialog esta a fechar
                {
                    this.resetUserInfo ()
                    this.$store.dispatch('updateTheUserInArray', this.userAuth)
                    this.$store.dispatch('setLoading', false)
                    this.$store.dispatch('getAllUsers')
                }

            },
        },
        methods: {
            saveProfissional (user)
            {

                if (this.end_at)
                {
                    let today = new Date();

                    user.professionalHistory = { //historico profissional do user.

                        created_at: (user.professionalHistory) ? user.professionalHistory.created_at : today, //Dia em que se registou os dados
                        updated_at: today, //Ultimo Dia de actualizacao dos dados
                        periods: (user.professionalHistory) ? user.professionalHistory.periods : [], //periodos em que ele esteve numa certa carreira profissional

                    } //historico profissional do user.

                    if (this.editPeriod) //se sim, estamos a editar
                    {
                        let periods = user.professionalHistory.periods

                        periods[ periods.indexOf(this.editPeriod)].start_at = this.start_at
                        periods[ periods.indexOf(this.editPeriod)].end_at = this.end_at
                        periods[ periods.indexOf(this.editPeriod)].institution = this.institution
                        periods[ periods.indexOf(this.editPeriod)].task = this.task
                        periods[ periods.indexOf(this.editPeriod)].details = this.details

                        user.professionalHistory.periods = periods //periodos actualizados
                    }
                    else
                    {
                        //TODO: Colocar varivais para start_at, end_at,.... para professionalHistory Object.
                        user.professionalHistory.periods.push({

                            start_at: this.start_at, //Data de comeco, dia em que eh activada a sua conta como profissional, uma data a ser enviada.
                            end_at: this.end_at, //Data em que expira a conta profissional, sera calculado em funcao dos dias passados
                            institution: this.institution,
                            task: this.task,
                            details: this.details,

                        })
                    }


                    this.showProcess = true
                    this.$store.dispatch('updateOnlineUserData', user)
                }
                else
                {
                    alert('Por favor, selecione a data de INICIO e do FIM')
                }

            },
            saveStartDate (date) {
                let startDay = new Date(date)
                let today = new Date()

                let d1 = new Date(startDay.getFullYear(), startDay.getMonth(), startDay.getDate()); //data da visualizacao
                let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                if (d1.getTime() <= d2.getTime()) //Ele pode gravar essadata como data de inicio, depois de hoje
                {
                    this.start_at = date
                    this.modal = false
                }
                else
                {
                    alert('Sobre a sua carreira, escreva apenas aquilo que ja aconteceu, Essa data de Inicio nao eh valida, insira apenas carreiras passadas cujas datas sao antes de hoje!')
                }
            },

            saveEndDate (date) {
                let startDay = new Date(this.start_at)
                let endDay = new Date(date)

                let d1 = new Date(startDay.getFullYear(), startDay.getMonth(), startDay.getDate()); //data da visualizacao
                let d2 = new Date(endDay.getFullYear(), endDay.getMonth(), endDay.getDate()); //data de hoje

                if (d1.getTime() <= d2.getTime()) //Ele pode gravar essadata como data de inicio, depois da data de fim
                {
                    this.end_at = date
                    this.modalEnd = false
                }
                else
                {
                    alert('A data de fim dessa tarefa nao pode ser antes da data de inicio!')
                }
            },


            onSave () {

                let user = this.userAuth

                user.Locations = {
                    province:               this.province,
                    city:                   this.city,
                    municipalDistrict:      this.isFromCity ? this.municipalDistrict : '',
                    district:               this.isFromCity || this.isFromMaputoProvince ? '' : this.district,
                    isFromMaputoCity:       this.isFromCity,
                    isFromMaputoProvince:   this.isFromMaputoProvince,
                    post:                   this.isFromCity ? '' : this.post,
                    bairro:                 this.isFromCity || this.isFromMaputoProvince ? this.bairro : '',
                    locality:               this.isFromCity || this.isFromMaputoProvince ? '' : this.locality,
                }

                this.showLocationProcess = true
                this.$store.dispatch('updateOnlineUserData', user)

            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            onCancel () {
                this.showProcess = false
                this.$store.dispatch('setLoading', false)
                this.dialog = false
            },

            inicializeUserInfo () {

                if (!this.isMyProfile) return

                this.start_at = this.editPeriod.start_at
                this.end_at = this.editPeriod.end_at
                this.institution = this.editPeriod.institution
                this.task = this.editPeriod.task
                this.details = this.editPeriod.details

            },

            resetUserInfo () {

                this.start_at = null
                this.end_at = null
                this.institution = ''
                this.task = ''
                this.details = ''

            },

            onHelp () {
                this.dialog = false
                this.$router.push('/help')
            }
        }
    }
</script>


