<template>
    <v-list v-if="explainClassesDetails">

        <v-subheader>{{ isMyProfile ? 'Niveis que eu explico' : 'Niveis que explica:' }}</v-subheader>

        <v-chip small="" style="font-size: 10px" v-for="item in explainLevels" v-bind:key="item">
            <v-avatar class="blue" style="font-size: 12px">
                {{ item.charAt(0).toString().toUpperCase() + item.charAt(1).toString().toUpperCase() }}
            </v-avatar>
            {{ item }}
        </v-chip>

        <template>
            <v-subheader v-if="$router.currentRoute.name === 'Profile'" class="mt-5">
            {{ isMyProfile ? 'Disciplinas que explicas por classe ou niveis' : 'Disciplinas por classe ou nivel' }}
            </v-subheader>
            <template v-else="">
                <v-subheader class="mt-2" style="font-size: 14px">
                    <span><span class="blue--text ml-1"> {{ nivel !== '' ? nivel : nivel = selectedNivel }}</span></span>

                    <v-flex>
                        <v-btn v-if="nivel === selectedNivel" outline="" round="" small="" @click="toggleNivel()" style="float: right;font-size: 10px">Ver todos niveis</v-btn>
                        <v-btn v-else="" outline="" round="" flat="" small="" @click="toggleNivel()" style="float: right;font-size: 10px">Ver apenas {{ selectedNivel }}</v-btn>
                    </v-flex>

                </v-subheader>
            </template>
        </template>

        <v-divider inset="" class="mb-3 mt-1"></v-divider>
        <template v-for="(item, i) in explainClassesDetails">

            <template v-if="item.level === nivel || nivel === 'Todos niveis' ||  $router.currentRoute.name === 'Profile'">
                <v-subheader>
                    {{ item.classe.length <= 4 ? item.classe : item.level }}  - ( {{ item.subjects.length }} disciplinas )
                </v-subheader>

                <template v-for="(subject, i) in item.subjects" v-if="subject">

                    <v-list-tile @click="">

                        <v-list-tile-action>
                            <v-icon>library_books</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>{{ subject }}</v-list-tile-title>
                        </v-list-tile-content>

                    </v-list-tile>
                </template>

                <!--<v-divider inset v-if="i === item.subjects.length -1"></v-divider>-->
            </template>

        </template>

    </v-list>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        props: ['user', 'selectedNivel'],
        mounted ()
        {

//            Evento emitido no Component/home.vue
            eventBus.$on('selectedNivel',  (value) => {

                this.nivel = value

            })

        },
        data ()
        {
           return {
               nivel : '',
           }
        },
        computed: {
            options () { //usado para fazer o scroll
                return {
                    duration: 1000,
                    offset: 0,
                    nivel: '',
                    easing: 'easeInQuint'
                }
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
            explainLevels ()
            {
                return this.isExplicador ? this.user.Explicador.explainLevels : ''
            },
            img () {
                return this.isUser ? this.user.photoURL : '//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png'
            },
            nomeUser() {
                return this.isUser && this.user.name !== null && this.user.name !== '' ? this.user.name : 'Sem Nome'
            },

            isExplicador ()
            {
                if (!this.isUser) return false
                return this.user.Explicador && this.user.Explicador.explainLevels
            },

            explainClassesDetails () {

                if (!this.isExplicador) return false

                return this.user.Explicador.subjectDetails
            },

            hasSubjects () {

                return this.isExplicador && this.user.Explicador.explainLevels
            },
            personalInfoFillPercent ()
            {
                return this.hasSubjects ? 100 : this.isExplicador ? 20 : 5;
            },


        },
        methods: {

            toggleNivel ()
            {
                if (this.nivel === this.selectedNivel)
                {
                    this.nivel = 'Todos niveis'
                }
                else
                {
                    this.nivel = this.selectedNivel
                }

            }

        },

    }
</script>


