// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.

import Vue from 'vue'
import 'babel-polyfill'
import App from './App'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import VueAnalytics from 'vue-analytics'

import Vuex from 'vuex'
import { store } from "./store/index";
import * as firebase from 'firebase'
import VueProgressBar from 'vue-progressbar'

// Import dos componentes Vue
import HomeFabButton from './components/shared/HomeFabButtom.vue'
import AlertComponent from './components/shared/Alert.vue'
import SearchResult from './components/shared/SearchResult.vue'

//Algumas Dialogs
import ConfirmDialog from './components/dialogs/ConfirmDialog.vue'
import DialogImageUploadAndResize from './components/dialogs/ImageUploadAndResize.vue'
import PasswordResetLink from './components/dialogs/PasswordResetDialog.vue'
import AddDialog from './components/dialogs/AddDialog.vue'
import SugestionDialog from './components/dialogs/SugestionDialog.vue'
import LoginDialog from './components/dialogs/LoginDialog.vue'
import MenuRight from './components/menus/MainMenuRight.vue'
import MenuLeft from './components/menus/MainMenuLeft.vue'

import Explicador from './components/explicador/Explicador.vue' //Componente no home da pagina
import ExplicadorSubjectsDialog from './components/explicador/ExplicadorSubjectsDialog.vue' //Componente help no direction do explicador
import ExplicadorHelpDialog from './components/explicador/ExplicadorHelpDialog.vue' //Componente help no direction do explicador
import AuthDropdown from './components/dropdowns/AuthDropdown.vue'
import AppsDropdowns from './components/dropdowns/AppsDropdowns.vue'


//App components for - Bibliotecas institutions...
import RecomendationSugestion from './components/notifications/RecomendationSugestion.vue'
import Institution from './components/apps/institutions/Institution.vue'
import Library from './components/apps/libraries/Library.vue'

//Settings components
import UpdatePassword from './components/settings/PasswordUpdate.vue'
import MySubjectsUpdate from './components/settings/MySubjectsUpdate.vue'
import SendMail from './components/settings/SendMail.vue'
import Analitics from './components/settings/Analitics.vue'

// Otras dialogs
import AccountRemove from './components/dialogs/AccountRemove.vue'
import ProfilePhotoUpload from './components/profiles/ProfilePhotoUpload.vue'

//Pagina do Perfil
import OtherProfile from './components/profiles/OtherProfileUser.vue'
import MobileProfileSubjectsInfo from './components/profiles/MobileProfileSubjectsInfo.vue'
import ExplainSubjectsDetails from './components/profiles/ExplainSubjectsDetails.vue'
import MobileProfileAcademicInfo from './components/profiles/MobileProfileAcademicInfo.vue'
import MobileProfileLocationInfo from './components/profiles/MobileProfileLocationInfo.vue'
import MobileProfilePersonalInfo from './components/profiles/MobileProfilePersonalInfo.vue'
import OtherProfilePhotoInfo from './components/profiles/OtherProfilePhotoInfo.vue'
import MobileProfileContactInfo from './components/profiles/MobileProfileContactInfo.vue'
import MobileProfileAvailabilityInfo from './components/profiles/MobileProfileAvailabilityInfo.vue'
import MobileProfileProssionalHistoryInfo from './components/profiles/MobileProfileProssionalHistoryInfo.vue'

//Dialogs para alteracao de dados no perfil
import OtherProfileEditAcademicDialog from './components/dialogs/OtherProfileEditAcademicDialog.vue'
import OtherProfileEditPersonalDialog from './components/dialogs/OtherProfileEditPersonalDialog.vue'
import OtherProfileEditLocationDialog from './components/dialogs/OtherProfileEditLocationDialog.vue'
import OtherProfileEditProfissionalInfoDialog from './components/dialogs/OtherProfileEditProfissionalInfoDialog.vue'
import OtherProfileEditSubjectDialog from './components/dialogs/OtherProfileEditSubjectDialog.vue'
import OtherProfileShowContactDialog from './components/dialogs/OtherProfileShowContactDialog.vue'

Vue.component('home-fab-buttom',HomeFabButton)
Vue.component('snackbar',HomeFabButton)
Vue.component('sugestion-dialog',SugestionDialog)
Vue.component('confirm-dialog',ConfirmDialog)
Vue.component('dialog-image-upload-and-resize',DialogImageUploadAndResize)
Vue.component('app-alert',AlertComponent)
Vue.component('search-result',SearchResult) //Usado no App.vue, aparece como resultados de pesquisa.
Vue.component('add-dialog',AddDialog)
Vue.component('login-dialog',LoginDialog)

//Setting components
Vue.component('password-reset-dialog',PasswordResetLink)
Vue.component('account-remove', AccountRemove)
Vue.component('send-mail', SendMail)
Vue.component('analitics', Analitics)

//Administrator components
Vue.component('menu-right',MenuRight)
Vue.component('menu-left',MenuLeft)
Vue.component('explicador',Explicador)
Vue.component('explicador-subject-dialog',ExplicadorSubjectsDialog)
Vue.component('explicador-help-dialog',ExplicadorHelpDialog)
Vue.component('auth-dropdown',AuthDropdown)
Vue.component('apps-dropdown',AppsDropdowns)
Vue.component('update-password',UpdatePassword)
Vue.component('my-subjects-update',MySubjectsUpdate)

//relacionados ao perfil

Vue.component('other-profile',OtherProfile)
Vue.component('mobile-profile-subjects-info',MobileProfileSubjectsInfo)
Vue.component('explain-subjects-details',ExplainSubjectsDetails)
Vue.component('mobile-profile-academic-info',MobileProfileAcademicInfo)
Vue.component('mobile-profile-personal-info',MobileProfilePersonalInfo)
Vue.component('explicador-photo-info',OtherProfilePhotoInfo)
Vue.component('mobile-profile-contact-info',MobileProfileContactInfo)
Vue.component('mobile-profile-availability-info',MobileProfileAvailabilityInfo)
Vue.component('mobile-profile-profissional-history-info',MobileProfileProssionalHistoryInfo)
Vue.component('mobile-profile-location-info',MobileProfileLocationInfo)

// Edit Profile Component Dialog
Vue.component('explicador-edit-academic-dialog',OtherProfileEditAcademicDialog)
Vue.component('explicador-edit-personal-dialog',OtherProfileEditPersonalDialog)
Vue.component('explicador-edit-location-dialog',OtherProfileEditLocationDialog)
Vue.component('explicador-edit-subjects-dialog',OtherProfileEditSubjectDialog)
Vue.component('explicador-edit-profissional-info-dialog',OtherProfileEditProfissionalInfoDialog)
Vue.component('explicador-show-contact-dialog',OtherProfileShowContactDialog)
Vue.component('profile-photo-upload',ProfilePhotoUpload)

//Bibliotecas, Instituicoes ... components
Vue.component('recomendation-sugestion', RecomendationSugestion)
Vue.component('institution', Institution)
Vue.component('library', Library)

// Usando vuex
Vue.use(Vuex)

// Usando Vuetify e suas configuracoes personalizaveis
Vue.use(Vuetify, { theme: {
  primary: '#ee44aa',
  secondary: '#424242',
  accent: '#82B1FF',
  error: '#FF5252',
  info: '#2196F3',
  success: '#4CAF50',
  warning: '#FFC107'
}})

// Usando Progrees  bar e suas configuracoes  basicas
Vue.use(VueProgressBar, {
    color: 'rgb(143, 255, 199)',
    failedColor: 'red',
    height: '3px',
})


Vue.config.productionTip = false

//=========================================================
// Registando o evento bus, global para todos os ficheiros
//=========================================================
export const eventBus = new Vue()


//==========================================================
//====================== Filtros ===========================
//==========================================================
import AcademicInfoLimit from './filters/academicInfoLimit'
import NameLimit from './filters/nameLimit'
import StringLimit from './filters/stringLimit'

Vue.filter('filterInstitution', AcademicInfoLimit)
Vue.filter('filterName', NameLimit)
Vue.filter('filterStrLimit', StringLimit)


/* eslint-disable no-new */
new Vue({

      el: '#app',
      router, store,
      components: { App },
      template: '<App/>',

    beforeCreate () {

        // Initialize Explicador.co.mz
        let config = {
            apiKey: "AIzaSyDBfaoaVP6S4_fWwzsSzKhR3rc-fiMtY9w",
            authDomain: "explicador-284b3.firebaseapp.com",
            databaseURL: "https://explicador-284b3.firebaseio.com",
            projectId: "explicador-284b3",
            storageBucket: "gs://explicador-284b3.appspot.com/",
            messagingSenderId: "357384306340"
        };

        firebase.initializeApp(config);

        firebase.auth().onAuthStateChanged((user) => {

            if (user) {

                this.$store.dispatch('autoSignIn', user)

            }

            //Quando user autentica-se actualizamos os users
            this.$store.dispatch('getAllUsers')

        })


        // const sgMail = require('@sendgrid/mail');
        // sgMail.setApiKey('RtPGROpCTxiLopVzOqBk9w');
        // const msg = {
        //     to: 'test@example.com',
        //     from: 'test@example.com',
        //     subject: 'Sending with SendGrid is Fun',
        //     text: 'and easy to do anywhere, even with Node.js',
        //     html: '<strong>and easy to do anywhere, even with Node.js</strong>',
        // };
        // sgMail.send(msg, false);



    }
})

//Google Analitics
Vue.use(VueAnalytics, {
    id: 'UA-118274174-1',
    router,
    autoTracking: {
        skipSamePath: true
    }
})



