<template>

    <v-container fluid class="white text-xs-center">

        <h1 class="mt-3">A página de Administrador</h1>

        <p><v-btn to="/" class="azul-principal white--text">Voltar</v-btn></p>
        <v-layout class="mb-4">
            <v-flex xs12 sm6 offset-md3>
                <v-btn flat="" class="" @click="explicadorType = 'Normais'"><v-icon v-if="explicadorType === 'Normais'">done</v-icon> Explicadores Normais</v-btn>
                <v-btn flat="" class="" @click="explicadorType = 'Fakul'"><v-icon v-if="explicadorType === 'Fakul'">done</v-icon> Explicadores da Fakul</v-btn>
            </v-flex>
        </v-layout>


        <!--Card central que lista os users-->
        <v-layout>

            <v-flex xs12 sm6 offset-sm3  class="">

                <v-card>
                    <v-layout>
                        <v-flex xs6>
                            <v-text-field
                                    class="ml-2"
                                    label="Pesquisar explicador pelo nome"
                                    v-model="searchExplicador"
                            ></v-text-field>
                        </v-flex>
                        <v-flex xs6>
                            <h2>Total: {{ filteredUsers(explicadorType).length }}</h2>
                        </v-flex>
                    </v-layout>
                </v-card>

                <template v-for="user in filteredUsers(explicadorType)">
                    <v-card class="ma-2">
                        <v-layout>

                            <v-flex md6 xs12>
                                <div class="text-xs-center">
                                    <v-chip close>
                                        <v-avatar>
                                            <img :src="user.photoURL" :alt="user.name">
                                        </v-avatar>

                                        {{ user.name ? user.name : 'Nao tem nome' }}

                                    </v-chip>

                                    <v-flex class="ma-1 ">
                                        <div><v-icon>mail</v-icon> {{ user.email }}</div>
                                        <div><v-icon>phone</v-icon> {{ user.telfone }}</div>
                                        <div v-if="showDetails">

                                            <v-flex xs12>

                                                <v-text-field
                                                        class="ml-2"
                                                        label="Valor pago"
                                                        type="number"
                                                        v-model="proPaidValue"
                                                ></v-text-field>

                                                <v-text-field
                                                        class="ml-2"
                                                        label="Numero de dias"
                                                        type="number"
                                                        v-model="proDatesQtd"
                                                ></v-text-field>

                                                <v-flex xs12>
                                                    <v-dialog
                                                            ref="dialog"
                                                            persistent
                                                            v-model="modal"
                                                            lazy
                                                            full-width
                                                            width="290px"
                                                            :return-value.sync="date"
                                                    >
                                                        <v-text-field
                                                                slot="activator"
                                                                :label="proStarAt ? proStarAt : 'Clique para selecionar a data de inicio'"
                                                                hint="A data de inicio deve ser hoje ou depois"
                                                                v-model="date"
                                                                prepend-icon="event"
                                                                readonly
                                                        ></v-text-field>
                                                        <v-date-picker header-color="blue" v-model="date" scrollable>
                                                            <v-spacer></v-spacer>
                                                            <v-btn flat color="primary" @click="modal = false">Cancel</v-btn>
                                                            <v-btn flat color="green" @click="saveDate(date)">OK</v-btn>
                                                        </v-date-picker>
                                                    </v-dialog>
                                                </v-flex>

                                                <v-text-field
                                                        v-if="proEndAt"
                                                        class="ml-2"
                                                        label="Data de fim"
                                                        disabled
                                                        v-model="proEndAt"
                                                ></v-text-field>
                                            </v-flex>

                                            <!--<v-btn flat block=""-->
                                                   <!--class="ml-3" color="green" type="submit"-->
                                                   <!--:disabled="loading && isLoginProcessing" :loading="loading && isLoginProcessing">-->
                                                <!--Entrar-->
                                                <!--<span slot="loader" class="custom-loader">-->
                                                            <!--<v-icon light>cached</v-icon>-->
                                                        <!--</span>-->
                                            <!--</v-btn>-->

                                            <v-flex xs12 class="ma-1">
                                                <v-btn
                                                    class="green" round="" block="" @click="getProAccount(user, true)"
                                                    :disabled="isUserProNow(user) || loading && showProcess" :loading="loading && showProcess">
                                                    Tornar Profissional

                                                    <span slot="loader" class="custom-loader">
                                                        <v-icon light>cached</v-icon>
                                                    </span>
                                                </v-btn>
                                            </v-flex>

                                        </div>

                                        <div><v-btn flat="" block="" @click="">Ver o perfil desse User</v-btn></div>
                                    </v-flex>

                                </div>
                            </v-flex>

                            <v-flex xs6 class="ml-2">

                                <v-layout>
                                    <v-flex xs4 class="ma-1">

                                        <!--Btn Atribuir Pro-->
                                        <v-btn
                                            :disabled="isUserProNow(user)"
                                            title="Atribuir conta Pro ao User"
                                            class="blue"
                                            round=""
                                            block=""
                                            @click="showDetails = !showDetails ">
                                            Atr. Pro
                                        </v-btn>

                                        <!--Btn Atribuir Pro-->
                                        <v-btn
                                            :disabled="user.testemonials && user.testemonials.is"
                                            title="Atribuir testemunho, isso mostra que o explicador ja explicou alguem e a pessoa gostou"
                                            class="green"
                                            round=""
                                            block=""
                                            @click="getTestemonial(user, true)">
                                            Atr. Test
                                        </v-btn>
                                        <v-btn
                                            :disabled="!(user.testemonials && user.testemonials.is)"
                                            title="Remover testemunho, Se por algum motivo  as pessoas nao gostam dele"
                                            class="green"
                                            round=""
                                            block=""
                                            @click="getTestemonial(user, false)">
                                            Rem. Test
                                        </v-btn>

                                    </v-flex>
                                    <v-flex xs4 class="ma-1">

                                        <!--Btn remover Pro-->
                                        <v-btn
                                            title="Cancelar a conta Pro do User"
                                            class="orange"
                                            round=""
                                            block=""
                                            :disabled="!isUserProNow(user) || loading && showProcess" :loading="loading && showProcess"
                                            @click="cancelProAccount(user)">
                                            Rem. Pro

                                            <span slot="loader" class="custom-loader">
                                                <v-icon light>cached</v-icon>
                                            </span>
                                        </v-btn>

                                    </v-flex>
                                    <v-flex xs4 class="ma-1 ">

                                        <!--Btn desativar-->
                                        <v-btn
                                            title="Forcar a indisponibilidade do User."
                                            class="red"
                                            block=""
                                            round=""
                                            :disabled="!isAvailableNow(user)"
                                            @click="forceUnavailabityOfUser(user)">
                                            Desativar -
                                        </v-btn>

                                    </v-flex>
                                </v-layout>

                            </v-flex>

                        </v-layout>
                    </v-card>
                </template>

            </v-flex>

        </v-layout>

        <p><v-btn to="/" class="azul-principal white--text">Voltar</v-btn></p>

    </v-container>

</template>

<script>
    import { eventBus } from '../../main'
    export default {
        beforeCreate() {

            this.$Progress.start()

        },
        mounted () {

            console.log('Admin component mounted')
            
        },
        data() {
            return {
                showProcess: false,
                isProAccount: true,
                date: '',
                explicadorType: 'Normais',
                proStarAt: '',
                proEndAt: '',
                proDatesQtd: 15,
                proPaidValue: 500,
                searchExplicador: '',
                showDetails: false,
                modal: false,
                dateValid: false,
                //Precarios: pro com nivel Superior = 500 MT
                //Precarios: pro com nivel Secundario = 300 MT
                //Precarios: pro com nivel Primario = 200 MT
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            users () {
                return this.$store.getters.getUsers
            },

            fakulUsers () {
                return this.$store.getters.getFakulUsers
            },


        },
        watch: {

            loading (value) {

                if (!value && !this.error && this.showProcess) {

                    let message = 'Parabens Nome, Voce acaba de activar a sua conta (Pro) assim para nos, voce eh um Explicador Profissional. ' +
                        'Iremos te recomendar para as pessoas te procurarem. Se nao desejar mais ser profissional, eh  so entrar em contacto pelo: info@explicador.co.mz. Obrigado.'

                    let text = 'As alteracoes para o User foram gravadas com sucesso!'
                    let color = 'green'
                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                    this.showProcess = false
                    this.dateValid = false
                    //Enviar email para o user.

                }
            },

        },
        methods: {

            filteredUsers (type) {

                if (type === 'Normais')
                {
                    return this.users.filter((user) => {
                        return (user.name && user.name.toLowerCase().includes(this.searchExplicador.toLowerCase()))
                    })
                }
                else {
                    return this.fakulUsers.filter((user) => {
                        return (user.name && user.name.toLowerCase().includes(this.searchExplicador.toLowerCase()))
                    })
                }

            },
            scrollTo (el) {
                document.querySelector(el).scrollIntoView({
                    behavior: 'smooth'
                });

            },

            getTestemonial(user, is)
            {

                let today = new Date();

                user.testemonials = {
                    created_at: user.testemonials ? user.testemonials.created_at : today,
                    updated_at: today,
                    is: is,
                    isDate: !user.testemonials ? today : user.testemonials.is !== is ? today : user.testemonials.isDate,
                }
                user.isNotMyProfile = 'isNotMyProfile' //indicador para nao actualizar o userAuth

                this.showProcess = true //para mostrar o processamento assim que terminar de gravar...
                this.$store.dispatch('updateOnlineUserData', user) //actualizando os dados do Utilizador
            },
            saveDate (date)
            {
                let startDay = new Date(date)
                let today = new Date()

                let d1 = new Date(startDay.getFullYear(), startDay.getMonth(), startDay.getDate()); //data da visualizacao
                let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                if (d1.getTime() >= d2.getTime()) //Ele pode gravar essadata como data de inicio, depois de hoje
                {
                    this.proStarAt = date
                    this.proEndAt = this.getEndDate(startDay, this.proDatesQtd)
                    this.modal = false
                    this.dateValid = true
                }
                else
                {
                    alert('A conta Pro deve comecar hoje ou nos proximos dias, nao antes, a data selecionada eh invalida!')
                }
            },

            getProAccount (user, is) {


                if (this.isUserProNow(user)) return alert('O Utilizador desejado ja eh pro')

                if (!this.dateValid) return alert('Por favor, insira uma data de inicio valida')

                if (!confirm('Tem certeza que deseja Realmente atribuir uma pacote profissional ao: ' +user.name+ '?')) return

                this.isProAccount = is
                let today = new Date();

//                NOTA: Preciso apenas da data de inicio e o numero de dias, depois somar com o nr de dia e mostrar a data de fim.

                user.proAccount = {

                    is: is, //Controla se eh ou nao profissional (trazer variavel)
                    isDate: (user.proAccount && user.proAccount.is !== is) ? today : user.proAccount ? user.proAccount.isDate : today, //Controla a data em que se altera o estado de ser ou nao profissional (trazer variavel)
                    created_at: (user.proAccount) ? user.proAccount.created_at : today, //Dia em que comecou a ser profissional
                    updated_at: today, //Ultimo Dia de actualizacao dos dados
                    periods: (user.proAccount) ? user.proAccount.periods : [],  //periodos em que ele eh profissional


                } //Conta Pro.

                let startDay = new Date(this.proStarAt)

                user.proAccount.periods.push({

                    start_at: startDay, //Data de comeco, dia em que eh activada a sua conta como profissional, uma data a ser enviada.
                    days: this.proDatesQtd, //Dias em a conta deve fica activa, dias a serem enviados.
                    end_at: this.proEndAt, //Data em que expira a conta profissional, sera calculado em funcao dos dias passados
                    paid: this.proPaidValue, //Valor em que o user pagou para se tornar profissional
                    userSaverId: this.userAuth.id

                })

//                user = this.saveProfissional(user) //gravando os dados profissionais
//
                user.isNotMyProfile = 'isNotMyProfile' //indicador para nao actualizar o userAuth

                this.showProcess = true //para mostrar o processamento assim que terminar de gravar...
                this.$store.dispatch('updateOnlineUserData', user) //actualizando os dados do Utilizador

                //Enviar email aqui.
                this.sendProAccountCreatedMail(user, this.proDatesQtd)

            },

            sendProAccountCreatedMail (user, qtdDias)
            {
                let message = 'Olá ' + user.name + ', a sua conta Pro de ' + qtdDias + ' dias no Explicador.co.mz acaba de ser activada. \n' +
                    '\n' +
                    'Apartir de hoje você é um explicador recomendado. \n' +
                    '\n' +
                    'Pedimos que explique muito bem as pessoas que te procurarem, para que confiem em ti e na plataforma. \n' +
                    '\n' +
                    'Pode saber mais sobre a sua conta Pro no seu perfil, acesse já!\n' +
                    '\n' +
                    '\n' +
                    '\n' +
                    '\n' +
                    'Obrigado.'

                let subject = 'Conta Pro activada'

                let emailObject = {
                    message: message,
                    subject: subject,
                    to: user.email,
                }

                this.$store.dispatch('sendMail', emailObject)

            },
            cancelProAccountMail (user)
            {
                let message = 'Olá ' + user.name + ', a sua conta Pro no Explicador.co.mz acaba de ser Cancelada. \n' +
                    '\n' +
                    '\n' +
                    'Para saber mais sobre essa ação entre em contacto!. \n' +
                    '\n' +
                    'Pelo: info@explicador.co.mz. \n' +
                    '\n' +
                    '\n' +
                    '\n' +
                    'Obrigado.'

                let subject = 'Conta Pro Cancelada'

                let emailObject = {
                    message: message,
                    subject: subject,
                    to: user.email,
                }

                this.$store.dispatch('sendMail', emailObject)

            },


            cancelProAccount (user) {

                if (!confirm('Tem certeza que deseja Cancelar a conta Pro para: ' +user.name+ '?')) return

                user.proAccount.is = false
                user.proAccount.isDate = new Date();

                user.isNotMyProfile = 'isNotMyProfile' //indicador para nao actualizar o userAuth

                this.showProcess = true //para mostrar o processamento assim que terminar de gravar...
                this.$store.dispatch('updateOnlineUserData', user) //actualizando os dados do Utilizador

                this.cancelProAccountMail(user)

            },

            forceUnavailabityOfUser (user) {

                if (!confirm('Tem certeza que deseja tornar Indisponivel para ele nao mais ficar visivel ao explicador: ' +user.name+ '?')) return

                user.Explicador.availability = {
                    available : false
                }

                //TODO: Informar ao User que ele foi desabilitado e os respectivos motivos. (ELe foi tornando indisponivel, mas se ele quiser pode entrar e activar).

                user.isNotMyProfile = 'isNotMyProfile' //indicador para nao actualizar o userAuth

                this.showProcess = true //para mostrar o processamento assim que terminar de gravar...
                this.$store.dispatch('updateOnlineUserData', user) //actualizando os dados do Utilizador
            },

            saveProfissional (user)
            {

                let today = new Date();

                user.professionalHistory = { //historico profissional do user.

                    created_at: (user.professionalHistory) ? user.professionalHistory.created_at : today, //Dia em que se registou os dados
                    updated_at: today, //Ultimo Dia de actualizacao dos dados
                    periods: (user.professionalHistory) ? user.professionalHistory.periods : [], //periodos em que ele esteve numa certa carreira profissional

                } //historico profissional do user.

                //TODO: Colocar varivais para start_at, end_at,.... para professionalHistory Object.
                user.professionalHistory.periods.push({

                    start_at: new Date (), //Data de comeco, dia em que eh activada a sua conta como profissional, uma data a ser enviada.
                    end_at: new Date (), //Data em que expira a conta profissional, sera calculado em funcao dos dias passados
                    institution: 'Explicador',
                    task: 'Disponibilizar a plataforma',
                    details: '',

                })

            },

            getEndDate (startDay, days)
            {
                days = parseInt(days)
                startDay.setDate(startDay.getDate() + days) //TODO: Verificar o caso de se passar 30 dias, mas alguns meses terminam com: 28, 29, 30 ou 31 ou tratar a situacao dos 30 dias.

                return new Date(startDay.getFullYear(), startDay.getMonth(), startDay.getDate()); //data de fim
            },

            isAvailableNow (user)
            {
                if (user.Explicador && user.Explicador.availability) {

                    return user.Explicador.availability.available
                }
                return true
            },

            isUserProNow (userToBePro)
            {
                let isPro = false
                let user = this.users.find((user) => {

                    return (user.email === userToBePro.email) && user.proAccount && user.proAccount.is

                })

                if (user) //Se tiver sido encontrado algum user com Objecto Pro
                {

                    let periods = user.proAccount.periods

                    let period = periods[ periods.length -1 ]

                    let today = new Date()
                    let endProDate = new Date(period.end_at)

                    let d1 = new Date(endProDate.getFullYear(), endProDate.getMonth(), endProDate.getDate()); //data da visualizacao
                    let d2 = new Date(today.getFullYear(), today.getMonth(), today.getDate()); //data de hoje

                    if (d1.getTime() > d2.getTime())
                    {
                        isPro = true
                    }

                }

                return isPro
            },


        },
    }
</script>
<style>

    .nota1 {
        font-size: 30pt;
        color: darkblue;
        border-left: solid darkblue medium;
        padding-left: 15px;
    }
    .nota2 {
        font-size: 12pt;
        font-weight: bold;
        color: grey;
        border-left: solid darkblue medium;
        padding-left: 15px;
    }
    .explicador-text-info {
        font-weight: 200;
        font-size: 14pt;
        color: red;
    }
</style>


