import { store } from "../../store/index";

export default (to, from, next) => {

    // Quanto a seguranca, melhor tratar no componente profile.


    // let profileURL = this.$route.name
    // console.log('going to route', to.path)
    // next ()

    if (to.path.toString().includes('login')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next({name: 'Login'})

    }
    else if (to.path.toString().includes('help')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next({name: 'Help'})

    }
    else if (to.path.toString().includes('register')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next({name: 'Register'})

    }
    else if (to.path.toString().includes('settings')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next({name: 'Settings'})

    }
    else if (to.path.toString().includes('admin')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next({name: 'Admin'})

    }
    else if (to.path.toString().includes('notifications')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next({name: 'Notifications'})

    }
    else if (to.path.toString().includes('contacts')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next({name: 'Contacts'})

    }
    else if (to.path.toString().includes('library')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next({name: 'Library'})

    }
    else if (to.path.toString().includes('institutions')) { //Validando quem acessa a parte administrativa, adicionar outros emails aqui e na auth isAdminstrator

        next({name: 'Institutions'})

    }
    else {

        next ()


    }

}


