import { store } from "../../store/index";
import firebase from 'firebase'

export default (to, from, next) => {

    const user = store.getters.userAuth

    store.dispatch('togleLoginPage',true)

    if (!user) {

        next ()

    } else {

        next({name: 'Profile'})

    }



}


