<template>
    <v-dialog v-model="dialog" :maxWidth="$vuetify.breakpoint.name === 'xs' || $vuetify.breakpoint.name === 'sm' ? '100%' : '900'">
        <v-avatar size="40px" title="Clique para alterar a sua foto de perfil" slot="activator" class="mr-3">
          <img
                style="cursor: pointer"
                v-if="isUser"
                :src="user.photoURL"
                alt=""
                >
            <img
               v-else
               src="//ssl.gstatic.com/s2/oz/images/sge/grey_silhouette.png"
               alt=""
               >
        </v-avatar>
        <v-card>
            <!--Titulo da dialog-->
            <v-card-title
               class="blue white--text title ma-0 pa-0 pl-2">
                Foto de perfil
                <v-spacer></v-spacer>
                <v-btn icon="" @click="dialog = false"><v-icon>close</v-icon></v-btn>
            </v-card-title>

            <!--Corpo  da dialog-->
            <v-container grid-list-sm class="pa-4">

                <!--Espaco para o reposrte dos erros-->
                <v-layout row v-if="error">
                    <v-flex xs12>
                        <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                    </v-flex>
                </v-layout>

                <!--Corpo com os botoes da dialog-->
                <v-layout row wrap class="mt-0">

                    <!--Botao de carregamento-->
                    <v-flex xs12 sm6 class="mt-0 grey">
                        <v-btn v-if="isMyProfile" block="" flat @click="onPickFile">
                            Buscar nova imagem
                            <v-icon right dark>cloud_upload</v-icon>
                        </v-btn>
                        <form>
                            <input
                                    type="file"
                                    style="display: none"
                                    ref="fileInput"
                                    name="file"
                                    accept="image/*"
                                    @change="onFilePicked">
                        </form>
                    </v-flex>
                    <v-flex class="text-xs-center" sm6>
                        <span class="subheader">Veja Como vai ficar:</span>
                    </v-flex>

                    <v-flex xs12 sm6>
                        <!--Espaco para a previsualizacao da imagem-->
                        <v-card height="365px" width="100%" class="mt-4">
                            <v-flex xs12 v-if="imageUrl !== ''">
                                <img v-if="isMyProfile" style="width: 100%; cursor: pointer" :src="imageUrl" @click="onPickFile" height="360">
                                <img v-else="" style="width: 100%; cursor: pointer" :src="imageUrl" height="100%" width="100%">
                            </v-flex>
                        </v-card>

                        <!--<v-btn flat="" style="float: right; margin-right: -2px">Definir como foto de perfil</v-btn>-->
                        <v-btn
                                flat
                                block
                                v-if="isMyProfile"
                                @click="uploadPhoto"
                                class="green--text"
                                :disabled="loading"
                                :loading="loading">

                            Salvar Imagem

                            <span slot="loader" class="custom-loader">
                                <v-icon light>cached</v-icon>
                            </span>
                        </v-btn>
                    </v-flex>
                    <v-flex sm6 class="mt-4">
                        <v-layout>

                            <v-flex xs6 :class="$vuetify.breakpoint.width > 360 ? 'ml-5 pl-5' : ''" >
                                <!--Espaco para a previsualizacao da imagem-->
                                <v-card width="100" height="100px" class="mt-4" style="border-radius: 100%">
                                    <v-flex xs12 v-if="imageUrl !== ''">
                                        <img v-if="isMyProfile" :src="imageUrl" height="96" width="96" style="border-radius: 100%">
                                    </v-flex>
                                </v-card>

                                <p class="mt-3 ml-1">Página Inicial</p>
                            </v-flex>

                            <v-flex xs6>
                                <!--Espaco para a previsualizacao da imagem-->
                                <v-card width="100" height="100px" class="mt-4">
                                    <v-flex xs12 v-if="imageUrl !== ''">
                                        <img v-if="isMyProfile" height="96" width="96" :src="imageUrl">
                                    </v-flex>
                                </v-card>

                                <p class="ma-3">Meu Perfil</p>
                            </v-flex>

                        </v-layout>
                    </v-flex>

                </v-layout>

            </v-container>

            <v-divider class="mb-2"></v-divider>

            <!--Action da dialog-->
            <v-card-actions class="pt-0">
                <v-spacer></v-spacer>
                <v-btn flat  v-if="isUserAuth" color="primary" @click="dialog = false; imageUrl = ''">Cancelar</v-btn>
                <v-btn
                        flat
                        v-if="isMyProfile"
                        @click="uploadPhoto"
                        class="green--text"
                        :disabled="loading"
                        :loading="loading">

                    Salvar Imagem

                    <span slot="loader" class="custom-loader">
                                <v-icon light>cached</v-icon>
                            </span>
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>
<script>
    import { eventBus } from '../../main'
    import axios from 'axios'
    export default {
        props: ['user', 'showDialog'],
        data () {
            return {
                validation_token: 'base64:dXeGr7/AvrT16YlEp35hYlGfv654OGdPvUbPBF7GphM=2018',
                dialog: false,
                imageUrl: '',
                image: null,
                showProcess: false,
                file: null,
                photoURLServer: null,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },

            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.user.id === this.userAuth.id
            },
        },
        watch: {
            dialog (value) {

                if (value) {

                    this.imageUrl = this.user !== null ? this.user.photoURL : ''

                } else {

                    this.$emit('closing')
                    this.dialog = false,
                    this.$store.dispatch('clearError')
                    this.$store.dispatch('setLoading',false)

                }
            },
            showDialog(value) {

                if (value)
                {
                    this.dialog = value
                }

            },
            loading (value) {

                if (!value && !this.error && this.showProcess) {

                    let text = "A sua imagem foi actualizada com sucesso..."
                    let color = 'green'
                    this.showProcess = false
                    this.dialog = false
                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
                }
            }

        },

        methods: {
            uploadPhoto () {

//                this.$store.dispatch('uploadPhoto', {image: this.image, photoURLServer:  ''})

                //Depois de implementar o upload para um server eh so descomentar essa linha.
                this.resizePhotoOnAPI()

            },

            resizePhotoOnAPI ()
            {
//                let urlServer = 'http://localhost:8000/api/sizeImage'
//                let urlServer = 'http://explicador-api.herokuapp.com'
//                let urlServer = 'https://explicador-api.herokuapp.com/api/sizeImage'
//                let urlServer = 'https://159.65.118.8/api/sizeImage'
                let urlServer = 'https://www.api.explicador.co.mz/api/sizeImage'

//                alert('vai iniciar o upload para este server: ' + urlOnline)
                //Pegando a foto  actual do user
                let photo = this.user.photoURLServer ? this.user.photoURLServer : ''

                if (this.user.photoURL !== this.imageUrl)
                {
                    this.$store.dispatch('setLoading', true) //Mostrando o processamentogit stat

                    axios.post(urlServer, {image:  this.imageUrl, photoURLServer: photo,
                        userId: this.user.id ,validation_token: this.validation_token})
                        .then((response) =>{


                            //Gravando  a foto no servidor
                            this.photoURLServer = response.data.photoURLServer
//                            console.log(response.data)
//                            alert('O upload foi concluido com sucesso ' + urlServer)

                            this.showProcess = true
                            return this.photoURLServer

                        })
                        .then((serverPhoto) => {

                            this.showProcess = true
                            this.$store.dispatch('setLoading',false)
//                            alert('dados gravados no server, agora iria gravar no firebase')
//                            alert('dados gravados no server, agora vai gravar no firebase')
                            //Carregando a imagem para firebase
                            //Depois de carregar no firebase,  de seguida serao gravadas gravada
                            this.$store.dispatch('uploadPhoto', {image: this.image, photoURLServer:  serverPhoto})

                        })
                        .catch((error)=>{
                            this.showProcess = false
                            console.log('Ocorreu um erro ao carregar a foto', error)
                            this.$store.dispatch('setLoading',false)
//                            this.$store.dispatch('setError', error)
                        });
                }
                else
                {
                    alert('Por favor, carregue uma imagem diferente...')
                    console.log('mesma imagem nao precisamos de upload...')
                }
            },

            onDismissed () {
                this.$store.dispatch('clearError')
            },
            onHelp () {
                this.dialog = !this.dialog
                this.$router.push('/help')
            },
            onPickFile ()  {
                this.$refs.fileInput.click()
            },
            createImage(file) {
                let reader = new FileReader();
                let vm = this;
                reader.onload = (e) => {
                    vm.image = e.target.result;
                };
                reader.readAsDataURL(file);
            },
            onFilePicked (event) {
                const files = event.target.files
                this.file = event.target.files[0]

                //pegar aqui

                if (files.length === 0) return //Se nenhum ficheiro tiver sido carregado.


                let filename = files[0].name
                if (filename.lastIndexOf('.') <= 0)
                {
                    return alert('Por favor adicione um ficheiro valido!')
                }
                const fileReader = new FileReader()
                fileReader.addEventListener('load', () => {
                    this.imageUrl = fileReader.result
                })
                fileReader.readAsDataURL(files[0])
                this.image = files[0]
            }
        }

    }
</script>


