<template>
    <div class="text-xs-center">
        <v-menu offset-y xs3>

            <v-btn
                icon
                large
                :title="isUserAuth ? userAuth.name : 'Voce nao esta autenticado'"
                slot="activator">
                <v-avatar size="32px" :class="{ 'mr-3' : $vuetify.breakpoint.width <= 340 }">

                    <img
                       :src="imgDefault"
                       alt=""
                    >
                </v-avatar>
            </v-btn>

            <!--Menu caso esteja auth-->
            <v-card v-if="isUserAuth" width="260px">
                <v-list style="height: 100px">
                    <v-list-tile avatar >
                        <v-list-tile-avatar class="cursor-pointer" title="Ir para meu perfil">
                            <img :src="imgDefault" alt="John">
                            <v-btn style="position: absolute; margin: 30px 0px" :class="isAvailable ? 'green--text' :  'red--text'" icon :title="availableText">
                                <v-icon v-if="isAvailable">visibility</v-icon>
                                <v-icon v-else="">visibility_off</v-icon>
                            </v-btn>
                        </v-list-tile-avatar>
                        <v-list-tile-content>
                            <v-list-tile-title>{{ isUserAuth ? userAuth.name : '' }}</v-list-tile-title>
                            <v-list-tile-title>{{ isUserAuth ? userAuth.email : '' }}</v-list-tile-title>

                            <!--Navegacao entre perfil e Home-->
                            <v-list-tile-sub-title class="cursor-pointer">
                                <template v-if="isAvailable">
                                    <v-icon class="blue--text">done</v-icon>
                                    <a block="" class="blue--text" title="Altere a sua disponibilidade no perfil" flat @click="goToProfile">Disponivel</a>
                                </template>
                                <template v-else-if="isExplicador">
                                    <v-icon class="red--text">close</v-icon>
                                    <a block="" class="red--text" title="Está no modo anónimo, pode alterar a sua disponibilidade no perfil" flat @click="goToProfile">Indisponivel</a>
                                </template>
                                <template v-else-if="isStudent">
                                    <v-icon class="blue--text">done</v-icon>
                                    <a block="" class="blue--text" title="Autenticado como estudante." flat @click="goToProfile">Estudante</a>
                                </template>
                                <template v-else-if="isOther">
                                    <v-icon class="blue--text">done</v-icon>
                                    <a block="" class="blue--text" title="Autenticado como Outro." flat @click="goToProfile">Outro</a>
                                </template>
                                <template v-else="">
                                    <!--<v-icon class="blue&#45;&#45;text">done</v-icon>-->
                                    <a block="" class="blue--text" title="Autenticado como Outro." flat @click="goToProfile"> - </a>
                                </template>

                            </v-list-tile-sub-title>

                        </v-list-tile-content>
                    </v-list-tile>
                    <v-list-tile>
                    </v-list-tile>

                </v-list>

                <v-divider></v-divider>

                <v-list class="pb-0">

                <!--Contactos-->
                <v-list-tile class="list-small-height" @click="goToProfile">
                    <v-list-tile-action>
                        <v-btn icon ripple>
                            <v-icon color="blue">verified_user</v-icon>
                        </v-btn>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Meu perfil</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                    <!--Contactos-->
                    <v-list-tile class="list-small-height" @click="goToRoute('Contacts')">
                        <v-list-tile-action>
                            <v-btn icon ripple>
                                <v-icon color="lighten-1">phone</v-icon>
                            </v-btn>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Contactos</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                <!--Recomendacoes-->
                <v-list-tile v-if="route === 'Home'" class="list-small-height" title="Recomendar a plataforma para alguém" @click="onRecomend">
                        <v-list-tile-action>
                            <v-btn icon ripple>
                                <v-icon color="lighten-1">near_me</v-icon>
                            </v-btn>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Recomedações</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                <!--Configurações-->
                <v-list-tile class="list-small-height" @click="goToRoute('Settings')">
                        <v-list-tile-action>
                            <v-btn icon ripple>
                                <v-icon color="lighten-1">settings</v-icon>
                            </v-btn>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Configurações</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <!--Ajuda-->
                <v-list-tile class="list-small-height" @click="goToRoute('Help')">
                    <v-list-tile-action>
                        <v-btn icon ripple>
                            <v-icon color="lighten-1">help</v-icon>
                        </v-btn>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Ajuda</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <!--Admin Page-->
                <v-list-tile v-if="isAdminUser" class="list-small-height" @click="$router.push('/admin')">
                    <v-list-tile-action>
                        <v-btn icon ripple>
                            <v-icon color="lighten-1">settings</v-icon>
                        </v-btn>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Admin Page</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

                <!--Sair-->
                <v-list-tile  class="list-small-height " @click="logOutUser">
                    <v-list-tile-action>
                        <v-btn icon ripple>
                            <v-icon color="lighten-1">input</v-icon>
                        </v-btn>
                    </v-list-tile-action>
                    <v-list-tile-content>
                        <v-list-tile-title>Sair</v-list-tile-title>
                    </v-list-tile-content>
                </v-list-tile>

            </v-list>

                <v-card-actions>
                    <!--<v-btn block="" color="primary" title="Sair da conta" flat >Sair</v-btn>-->
                </v-card-actions>
            </v-card>

            <!--Menu caso nao estiver Auth-->
            <v-card v-else="">
                <v-list class="">

                    <!--Login-->
                    <v-list-tile class="" @click="goToRoute('Login')">
                        <v-list-tile-action>
                            <v-icon color="lighten-1">account_circle</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Login</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <!--Register-->
                    <v-list-tile class="" @click="goToRoute('Register')">
                        <v-list-tile-action>
                            <v-icon color="lighten-1">supervisor_account</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Registar-se</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                    <!--Ajuda-->
                    <v-list-tile class="" @click="goToRoute('Help')">
                        <v-list-tile-action>
                            <v-icon color="lighten-1">help</v-icon>
                        </v-list-tile-action>
                        <v-list-tile-content>
                            <v-list-tile-title>Ajuda</v-list-tile-title>
                        </v-list-tile-content>
                    </v-list-tile>

                </v-list>
            </v-card>
        </v-menu>
    </div>
</template>
<script>
    import { eventBus } from '../../main'
    export default {
        data: () => ({
            fav: true,
            menu: false,
            message: false,
            hints: true
        }),
        computed: {

            imgDefault () {
                return this.$store.getters.getImg
            },
            userAuth () {
                return this.$store.getters.userAuth
            },
            isUserAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            user () {
                return this.$store.getters.user
            },
            isUser () {
                return this.user !== null && this.user !== undefined
            },
            isMyProfile () {
                return this.isUserAuth && this.isUser && this.userAuth.id === this.user.id
            },
            userPendingForAuth () {
                return this.$store.getters.userPendingForAuth
            },
            isPendingForAuth () {
                return this.userAuth !== null && this.userAuth !== undefined
            },
            isAdminUser ()
            {
                return this.isUserAuth && (this.userAuth.email === 'joseeugenioseie@hotmail.com'
                || this.userAuth.email === 'info@explicador.co.mz')
            },
            route () {
                return this.$route.name
            },
            isMyProfilePage () {
                return this.route === 'Profile' && this.isMyProfile
            },
            availableText ()
            {

                if (this.isUserAuth && this.userAuth.Explicador && !this.userAuth.Explicador.availability) return 'Disponível'

                if (this.isUserAuth && this.userAuth.Explicador && this.userAuth.Explicador.availability && this.userAuth.Explicador.availability.available)
                {
                    return 'Actualmente Disponível!'
                }
                else
                {
                    return 'Indisponivel - Voçê está no modo anónimo'
                }
            },
            isExplicador () {
                return (this.isUserAuth && this.userAuth.identities && this.userAuth.identities.isExplicador)
            },

            isStudent () {
                return !(this.isExplicador) && (this.isUserAuth && this.userAuth.identities && this.userAuth.identities.isStudent)
            },

            isOther () {
                return !(this.isExplicador) && !(this.isStudent) && (this.isUserAuth && this.userAuth.identities && this.userAuth.identities.isOther)
            },


            isAvailable ()
            {
                if (this.isUserAuth && this.userAuth.Explicador && !this.userAuth.Explicador.availability) return true

                return this.isUserAuth && this.userAuth.Explicador && this.userAuth.Explicador.availability && this.userAuth.Explicador.availability.available
            },
            route () {
                return this.$route.name
            },
        },
        watch: {
            userAuth (value) {

                if (value === null) //Se for true, entao o user saiu da sua conta
                {
                    this.notifyUser() //Entao notificamos a ele que ja nao esta mais aut
                }
            }
        },
        methods: {
            logOutUser () {
//                if (confirm('Deseja realmente sair da sua conta?'))
                this.$store.dispatch('logOutUser')

            },
            onRecomend ()
            {
                eventBus.$emit('sendRecomendation') //Emitindo evento para chamar a dialog para o envio da recomendacao,
            },
            goToProfile ()
            {
                if (this.isUserAuth)
                this.$router.push({name: 'Profile', params: {profile: this.userAuth.username}})
            },
            goToRoute (route)
            {
                this.$router.push({name: route})
            },
            notifyUser ()
            {
                let text = "Voce não está mais autenticado!"
                let color = 'blue'
                eventBus.$emit('showSnackbar', text, color, 5000); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
            }
        }
    }
</script>


