<template>

    <v-flex xs12 class="white photo-background">
        <v-container class="">
            <v-layout>
                <v-flex md12 class="text-xs-center white--text hidden-xs-only" style="">


                    <h1>
                        <img width="25" src="https://explicador.co.mz/assets/images/explicador-lapis-1-150x150.png"
                             alt="">
                        <span class="mb-2">Explicador</span>
                    </h1>
                    <h3>Os melhores explicadores estão aqui, <b class="blue--text">Entre já</b> junte-se a eles!</h3>

                    <!--<p class="ml-3">Ao criar a sua conta, voçê concorda com os termos e condições <a href="">Saiba mais</a></p>-->
                </v-flex>
                <v-flex xs12 md6 class="">
                    <v-layout row v-if="error" :class="{ 'mb-4 mt-4' : error }">
                        <v-flex class="red">
                            <app-alert @dismissed="onDismissed" :text="error.message"></app-alert>
                        </v-flex>
                    </v-layout>

                    <v-card class="pa-0 ma-0 mb-5 pb-4" :class="elevation" style="border-radius: 15px">
                        <!--Titulo do formulario-->
                        <v-card-text class="text-xs-center pb-3">
                            <!--<img src="https://explicador.co.mz/assets/images/explicador-lapis-1-150x150.png"-->
                                 <!--alt="">-->
                            <h4 style="color: darkgray;border-bottom: solid #eee 1px" class="pb-3">Entrar</h4>
                        </v-card-text>
                        <!--Corpo-->

                        <v-card-text class="ma-0 pa-2 pr-4 pl-4">
                            <v-container class="ma-0 pa-0">
                                <form @submit.prevent="onLogin">

                                    <v-layout row>
                                        <v-flex xs12>
                                            <v-btn small="" flat round block  class="yellow" outline @click="googleSignIn" :disabled="loading && isLoginProcessing">
                                                Google
                                            </v-btn>
                                        </v-flex>

                                        <v-flex xs12>
                                            <v-btn small="" flat round block  class="blue" outline @click="facebookSignIn" :disabled="loading && isLoginProcessing">
                                                Facebook
                                            </v-btn>
                                        </v-flex>
                                    </v-layout>

                                    <div class="text-xs-center mt-3 mb-">OR</div>

                                    <v-layout row>
                                        <v-flex xs12>
                                            <v-text-field
                                                    name="email"
                                                    label="Mail"
                                                    prepend-icon="mail"
                                                    v-model="email"
                                                    type="email"
                                                    required></v-text-field>
                                        </v-flex>
                                    </v-layout>
                                    <v-layout row>
                                        <v-flex xs12>
                                            <v-text-field
                                                    name="password"
                                                    label="Password"
                                                    prepend-icon="lock"
                                                    v-model="password"
                                                    type="password"
                                                    required></v-text-field>
                                        </v-flex>
                                    </v-layout>

                                    <v-layout row class="mt-2">

                                        <v-flex x12 sm3>
                                            <!--Botao cancelar-->
                                            <v-btn block style="height: 22px" small="" round="" flat="" outline="" to="/">
                                                Cancelar
                                            </v-btn>

                                        </v-flex>

                                        <v-flex xs12 sm3>
                                            <!--Botao Entrar-->
                                            <v-btn flat
                                                   small=""
                                                   round=""
                                                   outline=""
                                                   block=""
                                                   class="ml-3"
                                                   style="height: 22px"
                                                   color="green"
                                                   type="submit"
                                                   :disabled="loading && isLoginProcessing" :loading="loading && isLoginProcessing">
                                                Entrar
                                                <span slot="loader" class="custom-loader">
                                                            <v-icon light>cached</v-icon>
                                                        </span>
                                            </v-btn>

                                        </v-flex>

                                        <v-spacer></v-spacer>

                                        <v-flex xs1>

                                        </v-flex>

                                    </v-layout>

                                </form>
                            </v-container>
                        </v-card-text>

                        <!--Opcao de login para quem ja possui uma conta-->
                        <v-card-text class="text-xs-center">
                            <p><password-reset-dialog></password-reset-dialog></p>

                            <!--<small style="color: darkgray"> <v-icon>help</v-icon><v-btn block="" flat="" round="" router to="/login">Clique aqui para login</v-btn></small>-->

                            <p>Ainda não criou a sua conta? <a href="#" @click="onClick('Register')">Criar conta</a></p>
                        </v-card-text>
                    </v-card>

                </v-flex>
            </v-layout>
        </v-container>
    </v-flex>

</template>

<script>

    import { eventBus } from '../../main'
    export default {
        data () {
            return {
                email: '',
                password: '',
                isLoginProcessing: false,
                elevation: 'elevation-3'
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            }
        },
        watch: {
            loading (value)
            {
                if (!value)
                {
                    this.isLoginProcessing = false
                }
            }
        },
        methods: {
            onLogin () {
                //validar primeiro os dados de login
                this.isLoginProcessing = true
                this.$store.dispatch('loginUser', {email: this.email, password: this.password})
            },
            onDismissed () {
                this.$store.dispatch('clearError')
            },
            googleSignIn () {
                this.$store.dispatch('googleSignIn')
            },
            facebookSignIn () {
                this.$store.dispatch('facebookSignIn')
            }
            ,
            onClick (route)
            {
                this.$router.push({name: route})
            }
        },
        beforeCreate () {
//            eventBus.$emit('ocultarMenus', false); //Esse evento eh tratado no ficheiro App.vue,  eh onde sao ocultados os menus
            this.$Progress.start()
            this.$Progress.set(80)
        },
        mounted () {
        },
    }


</script>
<style scoped="">
    .photo-background{
        background: url(https://user-images.githubusercontent.com/18400142/41173831-98202a7e-6b58-11e8-84a8-73701e2f88c2.jpg) no-repeat;

    }
</style>


